<template>
  <div>
    <v-row>
      <v-col cols="3" class="hidden-sm-and-down">
        <category-section
          :colors="all_colors"
          :attributes="attributes"
          :categories="categories"
          :searchProduct="searchProduct"
          :maxprice="max_price"
          :minprice="min_price"
        ></category-section>
      </v-col>
      <v-col lg="8" sm="12" md="8">
        <div class="mt-5">
          <v-breadcrumbs :items="items" large></v-breadcrumbs>
        </div>
        <div class="text-h4">
          <v-list dense>
            <v-list-item dense>
              <v-row>
                <v-col lg="3" md="3" sm="6" class="filter">
                  <v-list-item-content>
                    <v-form @submit.prevent="filter">
                      <v-text-field
                        v-model="filterSearch"
                        outlined
                        label="Search"
                        append-icon="search"
                      ></v-text-field>
                    </v-form>
                  </v-list-item-content>
                </v-col>

                <v-col lg="3" md="3" sm="6" class="filter">
                  <v-list-item-content class="mr-1">
                    <v-select
                      v-model="selectedSort"
                      outlined
                      :items="sortby"
                      item-text="text"
                      item-value="value"
                      label="Sort By"
                      @change="filter"
                    ></v-select>
                  </v-list-item-content>
                </v-col>
                <v-col lg="3" md="3" sm="6" class="filter">
                  <v-list-item-content class="mr-1">
                    <v-select
                      v-model="brand"
                      outlined
                      :items="brands"
                      item-text="name"
                      item-value="slug"
                      @change="filter"
                      label="All Brands"
                    ></v-select>
                  </v-list-item-content>
                </v-col>
                <v-col lg="3" md="3" sm="6" class="filter">
                  <v-list-item-content>
                    <v-select
                      v-model="seller"
                      outlined
                      :items="sellers"
                      item-text="name"
                      item-value="id"
                      @change="filter"
                      label="All sellers"
                    ></v-select>
                  </v-list-item-content>
                </v-col>
              </v-row>
            </v-list-item>
          </v-list>

          <v-divider></v-divider>
        </div>

        <div class="mt-2">
          <v-row justify="space-around">
            <v-col
              lg="4"
              md="6"
              sm="6"
              v-for="(item, i) in products"
              :key="i"
              class="d-flex justify-center"
            >
              <search-product-card :item="item"></search-product-card>
            </v-col>
          </v-row>
          <div
            v-if="products == ''"
            class="mt-3 mb-5 text-center"
            style="height: 50px"
          >
            No products found
          </div>
          <div class="d-flex justify-center mb-5" style="text-decoration: none">
            <v-pagination
              v-model="page"
              :length="length"
              prev-icon="keyboard_arrow_left"
              next-icon="keyboard_arrow_right"
              @input="getResults"
            ></v-pagination>
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Axios from "axios";
import CategorySection from "../components/CategorySection";
import SearchProductCard from "../components/SearchProductCard";
import { baseURI, baseImgURI } from "../api/api";

export default {
  data() {
    return {
      brand: this.$route.query.brand,
      seller: this.$route.query.seller_id,
      filterSearch: this.$route.query.q,
      page: 1,
      length: 1,
      imageURI: "",
      brands: [],
      categories: [],
      sellers: [],
      sortingItems: ["Low To High", "High To Low", "New To Old", "Old To New"],
      products: [],
      items: [
        {
          text: "Home",
          disabled: false,
          href: "/",
        },
        {
          text: "Search",
          disabled: false,
          href: "#",
        },
        {
          text: "Products",
          disabled: true,
          href: "#",
        },
      ],
      selectedSort: this.$route.query.sort_by,
      sortby: [
        {
          text: "Newest",
          value: 1,
        },
        {
          text: "Oldest",
          value: 2,
        },
        {
          text: "Low To High",
          value: 3,
        },
        {
          text: "High To Low",
          value: 4,
        },
      ],
      attributes: [],
      all_colors: [],
      max_price: 0,
      min_price: 0,
      routeObj: {},
    };
  },
  methods: {
    fetchData() {
      let brandsApi = Axios.get(`${baseURI}/brands`);
      let sellerApi = Axios.get(`${baseURI}/shops`);
      let categoryApi = Axios.get(`${baseURI}/categories`);

      Axios.all([categoryApi, brandsApi, sellerApi]).then(
        Axios.spread((...res) => {
          this.categories = res[0].data.data;
          this.brands = res[1].data.data;
          this.sellers = res[2].data.data;
        })
      );
    },
    getResults(page = 1) {
      Axios.post(`${baseURI}/search?page=${page}`, this.$route.query).then(
        (res) => {
          this.max_price = res.data.max_price;
          this.min_price = res.data.min_price;
          this.products = res.data.products.data;
          this.length = res.data.products.last_page;
          this.attributes = res.data.attributes;
          this.all_colors = res.data.all_colors;
        }
      );
    },
    computeParam() {
      for (const key in this.$route.query) {
        if (Object.hasOwnProperty.call(this.$route.query, key)) {
          let element = this.$route.query[key];
          if (element == undefined) {
            element = "";
          }

          this.routeObj[key] = element;
        }
      }
      this.routeObj["sort_by"] = this.selectedSort;
      this.routeObj["brand"] = this.brand;
      this.routeObj["seller_id"] = this.seller;
      this.routeObj["q"] = this.filterSearch;
    },
    filter() {
      this.computeParam();
      this.searchProduct(
        Object.assign({ q: this.$route.query.q }, this.routeObj)
      );
    },

    searchProduct(obj) {
      this.$router
        .replace({
          name: "search",
          query: obj,
        })
        .catch(() => {});
    },
  },

  mounted() {
    this.imageURI = baseImgURI;
    this.getResults();
    this.fetchData();
  },
  components: {
    CategorySection,
    SearchProductCard,
  },
};
</script>
<style scoped>
  .filter{
    min-width: 150px;
  }
</style>