<template>
  <div>
    <div v-if="activeTab == 'Reviews'">
      <v-card elevation="0" class="mt-5">
        <v-card-title
          >Write your review
          <span class="caption"
            >(make sure you have bought this product)</span
          ></v-card-title
        >
        <v-card-text>
          <div>
            <v-list-item class="text-subtitle-2">
              Rate now
              <span
                ><v-rating
                  color="red"
                  v-model="stars"
                  hover
                  half-increments
                ></v-rating
              ></span>
            </v-list-item>
            <v-list-item>
              <v-textarea
                background-color="grey lighten-2"
                color="red"
                label="Write your review here"
                auto-grow
                filled
                clearable
                v-model="review"
              ></v-textarea>
            </v-list-item>

            <v-btn
              class="float-right text-capitalize"
              tile
              color="red"
              dark
              @click="onComment"
              >Post Review</v-btn
            >
          </div>
          <div class="mt-10" v-if="reviews != ''">
            <v-card-title> Customer Reviews </v-card-title>
            <v-card-text>
              <span>
                {{ avgRating | twoDecimal }} average based on
                {{ reviewCount }} reviews.
                <v-rating
                  v-model="avgRating"
                  color="red"
                  readonly
                  half-increments
                ></v-rating
              ></span>
              <v-row>
                <v-col cols="7">
                  <v-divider class="mt-5 mb-5"></v-divider>
                  <v-list dense>
                    <v-list-item dense v-for="(item, i) in pratings" :key="i">
                      <v-list-item-icon> {{ item.star }} Star</v-list-item-icon>
                      <v-list-item-content
                        ><v-progress-linear
                          :color="item.color"
                          height="15"
                          v-model="item.value"
                          background-color="grey lighten-4"
                        ></v-progress-linear
                      ></v-list-item-content>
                      <v-list-item-icon> {{ item.value }}</v-list-item-icon>
                    </v-list-item>
                  </v-list>
                </v-col>
              </v-row>
              <div class="text-h6">Reviews</div>
              <div>
                <v-list three-line>
                  <template>
                    <v-divider></v-divider>
                    <span
                      v-for="(item, index) in reviews"
                      :key="index"
                      v-show="index < count"
                    >
                      <v-list-item>
                        <v-list-item-avatar>
                          <v-img
                            v-if="item.image"
                            :src="imageURI + item.image"
                          ></v-img>
                          <v-img v-else class="bg grey"></v-img>
                        </v-list-item-avatar>

                        <v-list-item-content>
                          <div>
                            <v-list-item-title class="font-weight-bold"
                              >{{ item.name }}
                              <v-rating
                                readonly
                                color="red"
                                small
                                v-model="item.stars"
                              ></v-rating>
                            </v-list-item-title>
                          </div>
                          <v-list-item-subtitle
                            class="black--text font-weight-bold"
                            v-html="`Reviewed on: ${item.date}`"
                          ></v-list-item-subtitle>
                          <p class="caption">{{ item.review }}</p>
                        </v-list-item-content>
                      </v-list-item>
                      <v-divider inset></v-divider>
                    </span>
                  </template>
                </v-list>
                <div class="d-flex justify-center">
                  <v-btn
                    v-if="count == 3"
                    color="red"
                    class="text-capitalize"
                    tile
                    dark
                    @click="count = reviewCount"
                    >Show More <v-icon>keyboard_arrow_down</v-icon></v-btn
                  >
                  <v-btn
                    v-else
                    color="red"
                    class="text-capitalize"
                    tile
                    dark
                    @click="count = 3"
                    >Show Less <v-icon>keyboard_arrow_up</v-icon></v-btn
                  >
                </div>
              </div>
            </v-card-text>
          </div>
        </v-card-text>
      </v-card>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
import Swal from "sweetalert2";
export default {
  props: ["activeTab", "productID"],
  data() {
    return {
      imageURI: "",
      count: 3,
      stars: 0,
      review: "",
      ratings: [],
      pratings: [
        {
          color: "green",
          star: 5,
          value: 0,
        },
        {
          color: "blue",
          star: 4,
          value: 0,
        },
        {
          color: "blue lighten-3",
          star: 3,
          value: 0,
        },
        {
          color: "yellow",
          star: 2,
          value: 0,
        },
        {
          color: "red",
          star: 1,
          value: 0,
        },
      ],
      reviews: [],
      avgRating: 0,
      reviewCount: 0,
    };
  },
  methods: {
    fetchReviews() {
      axios
        .get(`${baseURI}/review/${this.productID}`)
        .then((res) => {
          this.reviews = res.data.reviews;
          this.ratings = res.data.ratings;
          this.avgRating = res.data.ratings.average;
          this.reviewCount = res.data.ratings.total;
          this.computeReview();
        })
        .catch(() => {});
    },
    computeReview() {
      this.pratings[0].value = this.ratings.five;
      this.pratings[1].value = this.ratings.four;
      this.pratings[2].value = this.ratings.three;
      this.pratings[3].value = this.ratings.two;
      this.pratings[4].value = this.ratings.one;
    },
    onComment() {
      if (localStorage.getItem("user_id")) {
        let reviewData = {
          product_id: this.productID,
          stars: this.stars,
          review: this.review,
        };
        if (reviewData.review != "" && reviewData.stars != "") {
          axios
            .post(`${baseURI}/review`, reviewData, {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("token_id")}`,
              },
            })
            .then((res) => {
              if (res.status == 201) {
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 2000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "success",
                  title: res.data.messsage,
                });
                this.fetchReviews();
              } else if (res.status == 200) {
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 2000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "info",
                  title: res.data.message,
                });
              }
            })
            .catch(() => {});
        } else {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 2000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "info",
            title: "Review data required",
          });
        }
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: "Authorization Required",
        });
      }
    },
  },
  mounted() {
    this.imageURI = baseImgURI;
    if (this.productID != undefined) {
      this.fetchReviews();
    }
    this.computeReview();
  },
};
</script>
