<template>
  <v-app>
    <navbar />
    <v-main>
      <div
        v-if="$root.loading"
        class="text-center my-12"
        style="height: 1000px"
      >
        <v-overlay>
          <v-progress-circular
            :size="70"
            :width="7"
            color="red"
            indeterminate
          ></v-progress-circular>
        </v-overlay>
      </div>
      <!-- routing view components -->
      <router-view v-else> </router-view>
    </v-main>

    <!-- footer -->
    <Footer />
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import { mapActions, mapMutations } from "vuex";
import Axios from "axios";
import { baseAuthURI, baseURI } from "./api/api";
export default {
  name: "App",

  components: {
    Navbar,
    Footer,
  },

  data: () => ({
    //
    value: 1,
  }),
  methods: {
    ...mapActions("CurrentUser", ["tryAutoLogin"]),
    ...mapMutations("CurrentUser", ["setUserInfo"]),
    ...mapActions("Wishlist", ["storeWishlist"]),
    getWishlists() {
      Axios.get(baseURI + "/wishlists/" + localStorage.getItem("user_id"), {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.storeWishlist(res.data.data);
      });
    },
    getUserInfo() {
      Axios.get(baseAuthURI + "/user", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.setUserInfo(res.data);
      });
    },
  },
  created() {
    if (localStorage.getItem("user_id")) {
      this.getWishlists();
      this.getUserInfo();
    }
    this.tryAutoLogin();
  },
};
</script>
