<template>
  <div>
    <v-row>
      <v-col lg="6" md="6" sm="12">
        <shipping-info></shipping-info>
      </v-col>
      <v-col lg="6" md="6" sm="12">
        <payment-info :handlingFee="handlingFee"></payment-info>
      </v-col>

      <v-col lg="8" md="8" sm="12">
        <order-items :order="'Items to Order'"></order-items>
      </v-col>
      <v-col lg="4" md="4" sm="12">
        <total-amount :handlingFee="handlingFee"></total-amount>
      </v-col>
    </v-row>

    <v-divider></v-divider>
    <v-row>
      <v-col class="d-flex justify-start"
        ><v-btn
          outlined
          color="grey"
          tile
          @click="changeTab('Payment', 1)"
          class="text-capitalize"
          ><v-icon>keyboard_arrow_left</v-icon> Back</v-btn
        ></v-col
      ><v-col class="d-flex justify-end">
        <v-btn
          :dark="!getStatus"
          color="red"
          tile
          v-on="on"
          v-bind="attrs"
          class="text-capitalize"
          @click="snackbar = true"
          :disabled="getStatus"
          >Complete Order</v-btn
        >
        <v-snackbar
          app
          v-model="snackbar"
          multi-line
          top
          timeout="-1"
          transition="scale-transition"
        >
          <template v-slot:action="{ attrs }">
            <v-btn
              color="pink"
              class="text-capitalize"
              text
              v-bind="attrs"
              @click="snackbar = false"
            >
              Close
            </v-btn>
          </template>
          <v-btn
            dark
            color="red"
            text
            class="text-capitalize"
            @click="submitOrder"
            >Proceed to Order</v-btn
          >
        </v-snackbar>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
import OrderItems from "../components/ShoppingCart";
import ShippingInfo from "../components/ShippingInfo";
import PaymentInfo from "../components/PaymentInfo";
import TotalAmount from "../components/TotalAmount";
import Axios from "axios";
import { baseURI } from "../api/api";
import Swal from "sweetalert2";
import { EventBus } from "../eventbus/event-bus";

export default {
  props: ["handlingFee"],
  data() {
    return {
      snackbar: "",
      cart: [],
      empty: false,
      showCompletionInfo: false,
    };
  },

  methods: {
    ...mapMutations("Checkout", ["completeOrder"]),
    ...mapMutations("Cart", ["clearCart"]),
    changeTab(data, n) {
      this.$emit("tab", data);
      this.$emit("e6", n);
    },

    submitOrder() {
      if (localStorage.getItem("user_id")) {
        let orderData = {
          user_id: parseInt(localStorage.getItem("user_id")),
          shipping_address: JSON.stringify(this.getAddress), //json encode(object to string) {name:'test'} =>>> {'name': 'test'}
          payment_type: "cash_on_delivery",
          payment_status: "unpaid",
          grand_total: this.getGrandTotal,
          coupon_discount: 0,
        };

        Axios.post(baseURI + "/order/store", orderData, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        })
          .then((res) => {
            if (res.data.success) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 2000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
              this.snackbar = false;
              this.completeOrder();
              setTimeout(() => {
                EventBus.$emit("cart", []);
              }, 1500);
              this.$router.push("/order-completion");
            }
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      } else {
        /* store order data */

        let address = this.getAddress;
        let name = address.name;
        let email = address.email;
        let orderData = {
          name: name,
          email: email,
          carts: this.getCartItems,
          shipping_info: address,
          payment_option: "cash_on_delivery",
          /* payment_status: "unpaid", */
          grand_total: this.getGrandTotal,
          coupon_discount: 0,
          coupon_id: 2,
        };
        Axios.post(`${baseURI}/guest-checkout`, orderData)
          .then((res) => {
            if (res.status == 200) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 2000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });

              this.snackbar = false;
              this.completeOrder();
              this.clearCart();
              this.$router.push("/order-completion");
            }
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      }
    },
  },
  computed: {
    ...mapGetters("Checkout", ["getAddress", "getGrandTotal"]),
    ...mapGetters("Cart", ["getCartItems"]),
    getStatus: {
      get() {
        EventBus.$on("redirectStatus", (status) => {
          if (status) {
            this.empty = status;
          }
        });

        EventBus.$on("cart", (cartItem) => {
          if (cartItem.length == 0) {
            this.empty = true;
          }
        });
        return this.empty;
      },
    },
  },

  components: {
    OrderItems,
    ShippingInfo,
    PaymentInfo,
    TotalAmount,
  },
};
</script>
