<template>
  <v-container>
    <v-row>
      <ProfileContent />

      <!-- if activeTab is wishlist -->
      <v-col lg="8" md="8" sm="6">
        <v-card elevation="5">
          <v-card-title class="bg grey lighten-2 mb-3"
            >Wishlist
            <span class="bg red px-2 ml-3"
              >{{ wishlist.length }} items</span
            ></v-card-title
          >
          <v-container>
            <v-simple-table fixed-header height="400px">
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">S/N</th>
                    <th class="text-left">Item</th>
                    <th class="text-left">Price</th>
                    <th class="text-left">Delete</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, i) in wishlist" :key="i">
                    <td>{{ i + 1 }}</td>
                    <td>
                      <v-card
                        max-width="344"
                        elevation="0"
                        class=""
                        :to="'/single/product/' + item.product.slug"
                      >
                        <v-list-item three-line>
                          <v-list-item-avatar tile size="50" color="grey">
                            <v-img
                              v-if="item.product.thumbnail_image != null"
                              :src="imageURI + item.product.thumbnail_image"
                            ></v-img>
                          </v-list-item-avatar>
                          <v-list-item-content>
                            <div class="text-subtitle-2">{{ item.product.name }}</div>
                          </v-list-item-content>
                        </v-list-item>
                      </v-card>
                    </td>
                    <td>
                      Base: {{ $currency }}.{{ item.product.base_price | twoDecimal }}
                      <br />
                      Discounted:
                      {{ $currency }}.{{
                        item.product.base_discounted_price | twoDecimal
                      }}
                    </td>
                    <td>
                      <v-btn x-small class="mt-2" text @click="deleteItem(item)"
                        ><v-icon color="red">delete</v-icon></v-btn
                      >
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import Swal from "sweetalert2";
import { EventBus } from "../eventbus/event-bus";
import ProfileContent from "../components/ProfileContent";
import Axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
import {mapMutations} from "vuex"
export default {
  data() {
    return {
      activeTab: "wishlist",
      imageURI: "",
      wishlist: [],
    };
  },

  methods: {
     ...mapMutations("Wishlist",["removeIdFromWishlist"]),
    getWishlists() {
      Axios.get(baseURI + "/wishlists/" + localStorage.getItem("user_id"), {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.wishlist = res.data.data;
      });
    },
    deleteItem(item) {
     
      Swal.fire({
        title: "Do you really want to delete the item from wishlist?",
        showDenyButton: true,
        showCancelButton: false,
        confirmButtonText: `Yes`,
        denyButtonText: `Cancel`,
      }).then((result) => {
        if (result.isConfirmed) {
          Axios.delete(baseURI + "/wishlists/" + item.id, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            },
          })
            .then((res) => {
              this.removeIdFromWishlist(item.product_id)
              this.getWishlists();
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 2000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
            })
            .catch(() => {
              Swal.fire("Deletion error", "", "error");
            });
        } else if (result.isDenied) {
          Swal.fire("Deletion cancelled", "", "info");
        }
      });
    },
  },

  created() {
    EventBus.$on("activeTab", (data) => {
      this.activeTab = data;
    });

    this.imageURI = baseImgURI;
    this.getWishlists();
  },

  components: {
    ProfileContent,
  },
};
</script>
