<template>
  <div>
    <v-system-bar
      :app="app"
      color="black"
      :height="height"
      :lights-out="lightsOut"
      :window="window"
      dark
      absolute
      class="systembar"
    >
      <v-row class="hidden-md-and-up">
        <v-col cols="6" class="d-flex justify-start align-center">
          <v-btn class="text-capitalize" @click.stop="systembar = true"
            ><v-icon>menu</v-icon>Menu</v-btn
          >
        </v-col>
        <v-col cols="6" class="d-flex justify-end align-center">
          <v-card class="d-flex flex-start align-center" to="#"
            ><v-icon>call</v-icon> <span>+977-9888-888-88 </span></v-card
          >
        </v-col>
      </v-row>

      <v-row class="hidden-sm-and-down">
        <v-spacer></v-spacer>
        <v-card class="d-flex flex-start align-center" to="#"
          ><v-icon>call</v-icon> <span>+977-9888-888-88 </span></v-card
        >
        <v-spacer></v-spacer>
        <v-row class="d-flex flex-end align-center">
          <v-card v-if="!auth" class="mr-3" to="/user/login">
            <v-icon>login</v-icon>
            <span>Login</span>
          </v-card>
          <v-card
            v-for="(item, i) in headerItems"
            :key="i"
            class="mr-3 pa-1"
            :to="item.link"
          >
            <v-icon>{{ item.icon }}</v-icon>
            <span>{{ item.label }} </span>
          </v-card>
          <v-card v-if="auth" to="/user/wishlist">
            <v-icon>favorite</v-icon>
            <span>Wishlist</span>
          </v-card>
        </v-row>
      </v-row> </v-system-bar
    ><!-- navbar -->
    <v-app-bar app color="white" height="90" elevate-on-scroll extended>
      <router-link to="/">
        <div class="logo">
          <v-img
            alt="Company Logo"
            src="https://zholaa.nextnepal.dev/public/backend/assets/images/zholaa_logo.png"
            transition="scale-transition"
            width="250"
          /></div
      ></router-link>

      <v-spacer></v-spacer>

      <search-bar class="hidden-sm-and-down"></search-bar>

      <v-spacer></v-spacer>
      <v-dialog>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="hidden-md-and-up"
            v-on="on"
            v-bind="attrs"
            color="red"
            dark
            text
            ><v-icon size="40">search</v-icon></v-btn
          >
        </template>
        <search-bar></search-bar>
      </v-dialog>

      <v-menu v-if="auth" offset-y open-on-hover>
        <template v-slot:activator="{ on, attrs }">
          <v-btn small text v-bind="attrs" v-on="on" class="hidden-xs-only">
            <v-icon>account_circle</v-icon>
          </v-btn>
        </template>
        <v-list dense width="100">
          <v-list-item
            v-for="(item, index) in userProfile"
            :key="index"
            :to="item.link"
          >
            <v-list-item-title>{{ item.label }}</v-list-item-title>
          </v-list-item>
          <v-list-item @click="onLogout">
            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            small
            text
            @click.stop="DrawerContent('product')"
            class="hidden-xs-only"
          >
            <v-badge
              overlap
              :content="cartItems.length != 0 ? cartItems.length : '0'"
              color="buttonColor"
              ><v-icon>shopping_bag</v-icon></v-badge
            >
          </v-btn>
        </template>
        <span>Cart</span>
      </v-tooltip>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            small
            text
            @click.stop="DrawerContent('compare')"
            class="hidden-xs-only"
          >
            <v-badge
              overlap
              :content="
                getCompareList.length != 0 ? getCompareList.length : '0'
              "
              color="buttonColor"
              ><v-icon>analytics</v-icon></v-badge
            >
          </v-btn>
        </template>
        <span>Compare</span>
      </v-tooltip>
      <v-spacer></v-spacer>

      <template v-slot:extension>
        <!-- categories navigation drawer -->

        <CategoriesDrawer />
      </template>
    </v-app-bar>
    <!-- left systembar menu -->
    <v-navigation-drawer
      temporary
      v-model="systembar"
      left
      fixed
      color="blue-grey lighten-5"
      width="220"
    >
      <v-divider></v-divider>
      <v-expansion-panels v-if="auth">
        <v-expansion-panel>
          <v-expansion-panel-header>
            <v-list dense>
              <v-list-item dense>
                <v-list-item-avatar>
                  <v-img :src="userInfo.avatar"></v-img>
                </v-list-item-avatar>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title> {{ userInfo.name }} </v-list-item-title>
                  <v-list-item-subtitle>{{
                    userInfo.email
                  }}</v-list-item-subtitle>
                </v-list-item-content>

                <!-- <template v-slot:actions>
                  <v-icon> keyboard_arrow_down </v-icon>
                </template> -->
              </v-list-item>
            </v-list>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-list dense>
              <v-list-item
                v-for="(item, index) in userProfile"
                :key="index"
                :to="item.link"
              >
                <v-list-item-icon>
                  <v-icon v-text="item.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title>{{ item.label }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
              <v-list-item @click="onLogout">
                <v-list-item-icon>
                  <v-icon>power_settings_new</v-icon>
                </v-list-item-icon>
                <v-list-item-content
                  ><v-list-item-title
                    >Logout</v-list-item-title
                  ></v-list-item-content
                >
              </v-list-item>
            </v-list>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
      <v-list dense>
        <v-list-item-group v-model="selectedItem" color="primary">
          <v-list-item
            v-for="(item, i) in systemitems"
            :key="i"
            :to="item.link"
          >
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.text"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item v-if="!auth" to="/user/login">
            <v-list-item-icon>
              <v-icon>login</v-icon>
            </v-list-item-icon>
            <v-list-item-content
              ><v-list-item-title>Login</v-list-item-title></v-list-item-content
            >
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <!-- Right side Drawer -->
    <v-navigation-drawer
      temporary
      v-model="drawer"
      right
      fixed
      color="blue-grey lighten-5"
      width="300"
    >
      <v-divider></v-divider>
      <div class="row ma-1">
        <div class="col d-flex align-center">
          <h3 v-if="drawerType == 'product'">Products</h3>
          <h3 v-else>Comparision</h3>
        </div>

        <div class="col mt-2 ml-4 d-flex align-center">
          <router-link
            to="/user/cart"
            v-if="drawerType == 'product'"
            class="zOrange--text text-decoration-none"
            ><p>
              View Cart<v-icon color="buttonColor">east</v-icon>
            </p></router-link
          >
          <router-link
            v-else
            to="/compare"
            class="zOrange--text text-decoration-none"
            ><p>
              Compare<v-icon color="buttonColor">east</v-icon>
            </p></router-link
          >
        </div>
      </div>
      <v-divider></v-divider>
      <div v-if="drawerType == 'product'">
        <v-list-item
          class="mb-1"
          three-line
          v-for="(item, i) in cartItems"
          :key="i"
          :to="'/single/product/' + item.product.slug"
        >
          <v-list-item-avatar tile size="80" color="grey"
            ><v-img :src="imageURI + item.product.image"></v-img>
          </v-list-item-avatar>
          <v-list-item-content>
            <div class="text-subtitle-2 text-truncate">
              {{ item.product.name }}
            </div>

            <v-list-item-subtitle
              >Price: Rs {{ item.price | twoDecimal }}</v-list-item-subtitle
            >
            <v-list-item-subtitle v-if="item.variation"
              >Variant: {{ item.variation }}</v-list-item-subtitle
            >
            <v-list-item-subtitle
              >Quantity: {{ item.quantity }}</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>
      </div>
      <v-divider class="mt-5"></v-divider>
      <div class="row ma-1" v-if="drawerType == 'product'">
        <v-list-item dense>
          <v-list-item-content>
            <v-list-item-title
              ><span class="font-weight-bold mr-1">Subtotal:</span
              >{{ $currency }}.
              {{ getTotalPrice | twoDecimal }}</v-list-item-title
            >
          </v-list-item-content>
          <v-list-item-icon>
            <v-btn
              elevation="2"
              color="buttonColor"
              small
              link
              tag="a"
              to="/checkout/address"
              class="text-capitalize"
              :dark="cartItems.length > 0 ? true : false"
              :disabled="cartItems.length > 0 ? false : true"
              >Checkout</v-btn
            >
          </v-list-item-icon>
        </v-list-item>
      </div>

      <div v-else>
        <v-list dense>
          <v-list-item
            dense
            three-line
            v-for="(item, i) in getCompareList"
            :key="i"
            :to="`/single/product/${item.slug}`"
          >
            <v-list-item-avatar tile size="80" color="grey"
              ><v-img :src="imageURI + item.image"></v-img
            ></v-list-item-avatar>
            <v-list-item-content>
              <div class="text-subtitle-2 text-truncate">
                {{ item.name }}
              </div>

              <v-list-item-subtitle
                >Price: Rs
                {{
                  item.base_price || item.unit_price | twoDecimal
                }}</v-list-item-subtitle
              >
              <v-list-item-subtitle>
                <v-rating
                  v-model="item.rating"
                  readonly
                  x-small
                  color="red"
                ></v-rating>
              </v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <div class="text-center mt-4" v-if="getCompareList != ''">
          <v-btn color="red" dark x-small @click="clearCompareList"
            >CLear compare list</v-btn
          >
        </div>
      </div>
    </v-navigation-drawer>
  </div>
</template>

<script>
import CategoriesDrawer from "./CategoriesDrawer";
/* import Swal from "sweetalert2"; */
import { mapMutations, mapActions, mapGetters } from "vuex";
import Axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
import SearchBar from "../components/SearchBar";
import { EventBus } from "../eventbus/event-bus";
export default {
  data() {
    return {
      focus: false,
      imageURI: "",
      cart: [],
      selectedItem: 1,
      systemitems: [
        { text: "Become Affiliate", icon: "accessibility", link: "/affiliate" },
        { text: "Customer Care", icon: "support_agent", link: "/contact" },
        { text: "Track Order", icon: "track_changes", link: "/track-order" },
        { text: "Sell on Zholaa", icon: "storefront", link: "/sell-on-zholaa" },
        { text: "Comparision", icon: "analytics", link: "/compare" },
      ],
      userProfile: [
        {
          label: "Profile",
          icon: "face",
          link: "/user/account",
        },
        {
          label: "Wishlist",
          icon: "favorite",
          link: "/user/wishlist",
        },
        {
          label: "Order",
          icon: "shopping_basket",
          link: "/user/orders",
        },
      ],
      app: true,
      drawer: false,
      systembar: false,
      drawerType: "",
      height: 50,
      lightsOut: false,
      window: false,
      search: "",

      headerItems: [
        {
          label: "Become Affiliate",
          icon: "accessibility",
          link: "/affiliate",
        },

        {
          label: "Customer Care",
          icon: "support_agent",
          link: "/contact",
        },

        {
          label: "Track Order",
          icon: "track_changes",
          link: "/track-order",
        },
        {
          label: "Sell On Zholaa",
          icon: "storefront",
          link: "/sell-on-zholaa",
        },
      ],
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
    this.imageURI = baseImgURI;
  },
  mounted() {
    this.fetchCartItems();
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    ...mapActions("CurrentUser", ["logout"]),
    ...mapMutations("Compare", [
      "clearCompareList",
      "removeFromComparelist",
      "clearCList",
    ]),
    ...mapMutations("Wishlist", ["clearWishlist"]),

    handleScroll() {
      this.app = !(window.scrollY > 0);
    },

    DrawerContent(data) {
      this.drawer = !this.drawer;
      this.drawerType = data;
    },
    removeProduct(item) {
      this.removeFromComparelist(item);
    },
    fetchCartItems() {
      if (localStorage.getItem("user_id")) {
        Axios.get(baseURI + "/carts", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((res) => {
            this.cart = res.data.data;
          })
          .catch(() => {});
      } else {
        this.fetchGuestCart();
      }
    },
    fetchGuestCart() {
      this.cart = this.getCartItems;
    },

    onLogout() {
      this.cart = this.getCartItems;
      this.clearWishlist();
      this.clearCList();
      this.logout();
    },
  },
  components: {
    CategoriesDrawer,
    SearchBar,
  },

  computed: {
    ...mapGetters("CurrentUser", ["isauthenticated", "userInfo"]),
    ...mapGetters("Cart", ["getCartItems", "getCartTotalPrice"]),
    ...mapGetters("Compare", ["getCompareList"]),
    auth() {
      return this.isauthenticated;
    },
    getTotalPrice() {
      let total = 0;
      if (this.cart) {
        this.cart.map((product) => {
          total += product.price * product.quantity;
        });
      }
      return total;
    },
    cartItems() {
      EventBus.$on("loggedIn", (status) => {
        if (status) {
          this.fetchCartItems();
        }
      });

      EventBus.$on("addedToCart", (status) => {
        if (status) {
          this.fetchCartItems();
        }
      });
      EventBus.$on("cart", (cart) => {
        this.cart = cart;
      });

      return this.cart;
    },
  },
};
</script>
<style scoped>
.logo {
  margin-left: 40px;
}
.v-list-item--active {
  color: brown;
}
.systembar {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
</style>
