<template>
  <div class="sheet" v-if="affiliations!=''">
    <v-container>
      <v-row justify="space-around">
        <v-col lg="8">
          <v-row>
            <v-col cols="6" class="font-weight-bold d-flex align-center">
              Affiliate Informations
            </v-col>
            <v-col cols="6" class="d-flex justify-end">
              <v-breadcrumbs :items="items" small></v-breadcrumbs>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-form ref="form" v-model="form">
        <v-row justify="space-around">
          <v-col lg="8" md="6" sm="6">
            <v-container>
              <v-card elevation="0">
                <v-card-title class="bg blue-grey lighten-5 text-subtitle-1"
                  >User Info</v-card-title
                >
                <v-card-text class="my-5">
                  <v-text-field
                    dense
                    type="text"
                    label="Name"
                    color="red"
                    outlined
                    :rules="nameRules"
                    v-model="vnames['name']"
                    append-icon="person"
                  ></v-text-field>
                  <v-text-field
                    dense
                    type="email"
                    label="Email"
                    color="red"
                    outlined
                    :rules="emailRules"
                    v-model="vnames['email']"
                    append-icon="email"
                  ></v-text-field>
                  <v-text-field
                    dense
                    type="password"
                    label="Password"
                    color="red"
                    outlined
                    :rules="passRules"
                    v-model="vnames['password']"
                    append-icon="lock"
                  ></v-text-field>
                  <v-text-field
                    dense
                    type="password"
                    label="Confirm Password"
                    color="red"
                    outlined
                    :rules="[
                      this.vnames['password'] ===
                        this.vnames['confirm_password'] ||
                        'Password must match',
                    ]"
                    v-model="vnames['confirm_password']"
                    append-icon="lock"
                  ></v-text-field>
                </v-card-text>
              </v-card>
            </v-container>
          </v-col>
          <v-col lg="8" md="6" sm="6">
            <v-container>
              <v-card elevation="0">
                <v-card-title class="bg blue-grey lighten-5 text-subtitle-1"
                  >Verification Info</v-card-title
                >
                <v-card-text class="my-5">
                  <span v-for="(item, i) in affiliations" :key="i">
                    <v-text-field
                      outlined
                      dense
                      color="red"
                      :label="item.label"
                      v-model="vnames[item.param]"
                      required
                      :rules="vrule"
                      v-show="item.type == 'text'"
                    ></v-text-field>
                    <!-- <v-text-field
                      outlined
                      dense
                      color="red"
                      :label="item.label"
                      v-model="vnames[item.param]"
                      required
                      :rules="emailRule"
                      v-show="item.label.toLowerCase() == 'email'"
                    ></v-text-field> -->
                    <v-text-area
                      outlined
                      dense
                      color="red"
                      :label="item.label"
                      v-model="vnames[item.param]"
                      required
                      :rules="vrule"
                      v-show="item.type == 'textarea'"
                    ></v-text-area>
                    <v-file-input
                      v-show="item.type == 'file'"
                      :label="item.label"
                      :rules="vrule"
                      v-model="vnames[item.param]"
                    ></v-file-input>
                    <v-select
                      outlined
                      dense
                      color="red"
                      :label="item.label"
                      :items="item.options"
                      item-text="text"
                      item-value="value"
                      v-model="vnames[item.param]"
                      :rules="vrule"
                      v-show="item.type == 'select'"
                    ></v-select>
                    <v-select
                      outlined
                      multiple
                      color="red"
                      :label="item.label"
                      :items="item.options"
                      chips
                      item-text="text"
                      item-value="value"
                      deletable-chips
                      v-model="multi[item.param]"
                      v-show="item.type == 'multi_select'"
                    >
                    </v-select>

                    <v-radio-group
                      v-model="vnames[item.param]"
                      v-show="item.type == 'radio'"
                      row
                      :rules="vrule"
                      mandatory
                    >
                      {{ item.label }}
                      <v-radio
                        v-for="rad in item.options"
                        :key="rad"
                        :label="rad"
                        :value="rad"
                      ></v-radio>
                    </v-radio-group>
                  </span>
                </v-card-text>
              </v-card>
            </v-container>
          </v-col>
        </v-row>
      </v-form>

      <v-col cols="12" class="d-flex justify-center">
        <v-btn
          tile
          color="buttonColor"
          :dark="form"
          :disabled="!form"
          class="text-capitalize"
          @click="saveData"
          >Save</v-btn
        >
      </v-col>
      <v-container class="mb-5">
        <v-row>
          <v-col lg="3" md="3" sm="6" v-for="(item, i) in policies" :key="i">
            <v-hover v-slot="{ hover }">
              <v-card
                :elevation="hover ? 3 : 0"
                outlined
                :class="{ 'on-hover': hover }"
                height="150"
                :to="item.link"
              >
                <v-list class="my-5">
                  <p class="text-center">
                    <v-icon color="zOrange">{{ item.icon }}</v-icon>
                  </p>
                  <p class="text-center">{{ item.title }}</p>
                </v-list>
              </v-card>
            </v-hover>
          </v-col>
        </v-row>
      </v-container>
    </v-container>
  </div>
</template>
<script>
import Axios from "axios";
import { baseURI } from "../api/api";
import Swal from "sweetalert2";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      form: true,
      vrule: [(v) => !!v || "Field is Required"],
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 30) || "Name must be less than 30 characters",
      ],
      emailRule: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      passRules: [
        (v) => !!v || "Password is required",
        (v) =>
          (v && v.length >= 8) || "Password must be more than 8 characters",
      ],

      items: [
        {
          text: "Home",
          href: "/",
        },
        {
          text: "Affiliate",
          href: "#",
        },
      ],

      multi: [],
      aff: [
        {
          type: "text",
          label: "Name",
          param: "element_0",
        },
        {
          type: "text",
          label: "Email",
          param: "element_1",
        },
        {
          type: "select",
          label: "Select",
          param: "element_2",
          options: ["option1", "option2"],
        },
        {
          type: "multi_select",
          label: "Multi select",
          param: "element_3",
          options: ["o1", "o2"],
        },
        {
          type: "multi_select",
          label: "Multi select 2",
          param: "element_4",
          options: ["osdad1", "oasdad2"],
        },
        {
          type: "file",
          label: "File",
          param: "element_4",
        },
        {
          type: "radio",
          label: "Radio",
          param: "element_5",
          options: ["aaa", "bbb"],
        },
      ],

      affiliations: [],
      affiliateform: {
        userinfo: {
          name: "",
          email: "",
          password: "",
          confirm_password: "",
        },
        verification: {},
      },
      formData: {},
      vnames: [],
    };
  },
  methods: {
    affiliate() {
      /* let obj = {};
      this.affiliations.forEach((element) => {
        obj[element.param] = element.label;
        if (element.type == "multi_select") {
          obj[element.param] = JSON.stringify(this.multi[element.param]);
        }
      });

      this.affiliateform.verification = obj;

      if (!this.auth) {
        this.formData = Object.assign(
          this.affiliateform.userinfo,
          this.affiliateform.verification
        );
      } else {
        this.formData = Object.assign(this.affiliateform.verification);
      } */

      Axios.post(`${baseURI}/affiliate/store`, this.formData)
        .then((res) => {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "success",
            title: res.data.message,
          });
        })
        .catch(() => {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 2000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "error",
            title: "Something went wrong",
          });
        });
    },
    saveData() {
      if (this.$refs.form.validate()) {
        let params = [];
        this.aff.forEach((element) => {
          if (element.type == "multi_select") {
            params.push(element.param);
          }
        });
        for (const key in this.vnames) {
          if (Object.hasOwnProperty.call(this.vnames, key)) {
            if (params.includes(key)) {
              delete this.vnames[key];
            }
          }
        }
        for (const key in this.multi) {
          if (Object.hasOwnProperty.call(this.multi, key)) {
            this.multi[key] = JSON.stringify(this.multi[key]);
          }
        }
        let obj = this.multi;
        this.multi = [];

        this.formData = Object.assign(this.vnames, obj);
        this.affiliate();
      }
    },
  },
  created() {
    setTimeout(() => {
      Axios.get(baseURI + "/affiliate")
        .then((res) => {
          this.affiliations = res.data;
        })
        .then(() => {
          for (let i = 0; i < this.affiliations.length; i++) {
            const element = this.affiliations[i];
            for (const key in element) {
              if (Object.hasOwnProperty.call(element, key)) {
                if (key == "options") {
                  let e = JSON.parse(element[key]);
                  element[key] = e;
                }
              }
            }
          }
        });
    }, 1500);
  },
  computed: {
    ...mapGetters("CurrentUser", ["isauthenticated", "userInfo"]),
    auth() {
      return this.isauthenticated;
    },
  },
};
</script>

