<template>
  <v-card :elevation="review == 'review' ? 0 : 5" outlined>
    <v-card-title class="bg grey lighten-2 mb-3" v-if="!order"
      >Shopping Cart
      <span class="bg zOrange px-2 ml-3 rounded"
        >{{ cartItems.length }} items</span
      >
      <v-spacer></v-spacer>
      Reward Point: {{ rewardpoint }}
    </v-card-title>
    <v-card-title class="bg grey lighten-2 mb-3" v-else
      >{{ order }}

      <span class="bg red px-2 ml-3">{{ cartItems.length }} items</span>
    </v-card-title>

    <v-card-text>
      <v-simple-table fixed-header height="270px">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">S/N</th>
              <th class="text-left">Item</th>
              <th class="text-left">Price</th>
              <th class="text-left">Quantity</th>
              <th class="text-left">Remove</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, i) in cartItems" :key="i">
              <td>
                {{ i + 1 }}
              </td>
              <td>
                <v-card
                  class="mx-2"
                  max-width="344"
                  elevation="0"
                  :to="'/single/product/' + item.product.slug"
                >
                  <v-list-item two-line>
                    <v-list-item-avatar tile size="70" color="grey">
                      <v-img :src="imageURI + item.product.image"></v-img>
                    </v-list-item-avatar>
                    <v-list-item-content>
                      <div class="subtitle-2">{{ item.product.name }}</div>

                      <v-list-item-subtitle v-if="item.variation"
                        >Variant:{{ item.variation }}</v-list-item-subtitle
                      >
                    </v-list-item-content>
                  </v-list-item>
                </v-card>
              </td>
              <td>{{ $currency }}.{{ item.price }}</td>
              <td>
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-bind="attrs"
                      v-on="on"
                      v-on:input="onChangeQuantity($event, item)"
                      outlined
                      dense
                      type="number"
                      color="red"
                      style="width: 80px"
                      flat
                      min="1"
                      :disabled="review == 'review' ? true : false"
                      :max="item.stock"
                      :value="item.quantity"
                      class="mt-5"
                    ></v-text-field>
                  </template>
                  <span
                    >Available stock
                    {{ item.product.available_stock || item.stock }}</span
                  >
                </v-tooltip>
              </td>
              <td>
                <v-btn x-small text @click="deleteItem(item)"
                  ><v-icon color="zOrange">remove_shopping_cart</v-icon></v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>

      <div v-if="cartItems.length != 0" class="d-flex justify-end mt-5">
        <v-btn
          color="buttonColor"
          dark
          class="text-capitalize"
          to="/checkout/address"
          v-if="!order"
          >Checkout</v-btn
        >
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
import Swal from "sweetalert2";
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
import { mapGetters, mapMutations } from "vuex";
import { EventBus } from "../eventbus/event-bus";
export default {
  props: ["order", "review"],
  data() {
    return {
      cartItems: [],
      selectedCartItem: [],
      imageURI: "",
      rewardpoint: "",
    };
  },
  methods: {
    ...mapMutations("Cart", ["changeQty", "removeProduct"]),
    fetchCartItems() {
      if (localStorage.getItem("user_id")) {
        Axios.get(baseURI + "/carts", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((res) => {
            this.cartItems = res.data.data;
            if (res.status == 200) {
              setTimeout(() => {
                EventBus.$emit("cart", this.cartItems);
              }, 1500);
              this.$emit("orderSize", this.cartItems.length);
            }
          })
          .catch(() => {});
      } else {
        this.fetchGuestCart();
      }
    },
    fetchGuestCart() {
      this.cartItems = this.getCartItems;
      setTimeout(() => {
        EventBus.$emit("cart", this.cartItems);
      }, 1500);
      this.$emit("orderSize", this.cartItems.length);
    },
    onChangeQuantity(event, item) {
      let qty = parseInt(event);

      let newItem = {
        id: item.id,
        product_id: item.product_id,
        quantity: qty,
      };

      setTimeout(() => {
        if (localStorage.getItem("user_id")) {
          Axios.post(baseURI + "/carts/change-quantity", newItem, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          })
            .then((res) => {
              if (res.status == 200) {
                this.fetchCartItems();
                setTimeout(() => {
                  EventBus.$emit("cart", this.cartItems);
                }, 1000);

                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 2000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "success",
                  title: res.data.message,
                });
              }
            })
            .catch((err) => {
              if (err.response.status == 400) {
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 2000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "error",
                  title: err.response.data.message,
                });
              }
            });
        } else {
          /* local */
          let product = {
            id: item.product_id,
            quantity: qty,
            stock: item.stock,
          };
          this.changeQty(product);
        }
      }, 1000);
    },

    deleteItem(cartItem) {
      Swal.fire({
        title: "Do you really want to remove the item from cart?",
        showDenyButton: true,
        showCancelButton: false,
        confirmButtonText: `Yes`,
        denyButtonText: `Cancel`,
      }).then((result) => {
        if (result.isConfirmed) {
          /* this.removeProduct(item); */

          if (this.auth) {
            /* server remove */
            Axios.delete(baseURI + "/carts/" + cartItem.id, {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("token_id")}`,
                "Content-Type": "application/json",
                Accept: "application/json",
              },
            })
              .then((res) => {
                if (res.status == 200) {
                  this.fetchCartItems();
                  setTimeout(() => {
                    EventBus.$emit("cart", this.cartItems);
                  }, 1000);
                  const Toast = Swal.mixin({
                    toast: true,
                    position: "top-end",
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                      toast.addEventListener("mouseenter", Swal.stopTimer);
                      toast.addEventListener("mouseleave", Swal.resumeTimer);
                    },
                  });

                  Toast.fire({
                    icon: "success",
                    title: res.data.message,
                  });
                }
              })
              .catch(() => {});
          } else {
            /* local remove */
            this.removeProduct(cartItem);
          }

          setTimeout(() => {
            if (this.cartItems.length == 0) {
              EventBus.$emit("redirectStatus", true);
            }
          }, 1500);
        } else if (result.isDenied) {
          Swal.fire("Removal cancelled", "", "info");
        }
      });
    },
    fetchRewardPoint() {
      Axios.get(`${baseURI}/reward-point-check`).then((res) => {
        /*  console.log("reward point",res); */
        this.rewardpoint = res.data;
      });
    },
  },
  mounted() {
    this.imageURI = baseImgURI;
    this.fetchCartItems();
    this.fetchRewardPoint();
  },
  computed: {
    ...mapGetters("CurrentUser", ["isauthenticated"]),
    ...mapGetters("Cart", ["getCartItems"]),
    auth() {
      return this.isauthenticated;
    },
  },
};
</script>
