import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import vuetify from "./plugins/vuetify";

import ProductCard from "./components/ProductCard"
import PaginatedProductCard from "./components/PaginatedProductCard"
import JustForYouProductCard from "./components/JustForYouProductCard"
import ProductSheet from "./components/ProductSheet"
import TitleRow from "./components/TitleRow"
import PaginatedProducts from "./components/PaginatedProducts"
import CategorySection from "./components/CategorySection";
import PaginatedCategorySection from "./components/PaginatedCategorySection"

export const EventBus = new Vue()


import VueCountdownTimer from "vuejs-countdown-timer";
Vue.use(VueCountdownTimer);



Vue.filter('twoDecimal', function (val) {
  return parseFloat(val).toFixed(2);
})

Vue.prototype.$currency = 'Rs'

Vue.component('title-row', TitleRow)
Vue.component('product-sheet', ProductSheet)
Vue.component('product-card', ProductCard)
Vue.component('paginated-product-card', PaginatedProductCard)
Vue.component('just-for-you-product-card', JustForYouProductCard)
Vue.component('category-section', CategorySection)
Vue.component('paginated-products', PaginatedProducts)
Vue.component('paginated-category-section', PaginatedCategorySection)

Vue.config.devtools = false
Vue.config.debug = false
Vue.config.silent = true


const app = new Vue({
  data: { loading: false },
  router,
  store,
  vuetify,
  render: (h) => h(App),
  mounted: () => document.dispatchEvent(new Event("x-app-rendered")),
}).$mount("#app");


router.beforeEach((to, from, next) => {
  app.loading = true
  next()
})

router.afterEach(() => {
  setTimeout(() => app.loading = false, 3000)
})