<template>
  <v-container>
    <v-row>
      <ProfileContent />
      <v-col lg="8" md="8" sm="6">
        <v-dialog v-model="addnewaddress">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col class="d-flex justify-end"
                ><v-btn
                  v-bind="attrs"
                  v-on="on"
                  color="red"
                  dark
                  class="text-capitalize"
                >
                  Add New Address <span><v-icon>add_location_alt</v-icon></span>
                </v-btn></v-col
              >
            </v-row>
          </template>
          <keep-alive>
            <v-card>
              <v-card-title class="bg red white--text"
                >Add New Address
              </v-card-title>
              <v-card-text class="mt-3">
                <v-form ref="form" v-model="addaddressform">
                  <v-row>
                    <v-col lg="4" md="4" sm="6" class="addresscol">
                      Country
                      <v-text-field
                        v-model="user.country"
                        required
                        outlined
                        dense
                        :rules="[!!this.user.country || 'Country is required']"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="4" md="4" sm="6" class="addresscol">
                      City
                      <v-text-field
                        v-model="user.city"
                        required
                        outlined
                        dense
                        :rules="[!!this.user.city || 'City is required']"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="4" md="4" sm="6" class="addresscol">
                      Postal Code
                      <v-text-field
                        v-model="user.postal_code"
                        required
                        outlined
                        dense
                        :rules="postalRule"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="4" md="4" sm="6" class="addresscol">
                      Phone
                      <v-text-field
                        type="number"
                        v-model="user.phone"
                        required
                        outlined
                        dense
                        :rules="phoneRule"
                        class="mt-2"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="8" md="8" sm="12" class="addresscol">
                      Address
                      <v-text-field
                        v-model="user.address"
                        required
                        outlined
                        dense
                        :rules="[!!this.user.address || 'Address is required']"
                        class="mt-2"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>

                <v-card-actions>
                  <v-btn
                    class="mt-2 text-capitalize"
                    color="red"
                    dark
                    @click="addnewaddress = false"
                    >Close</v-btn
                  >
                  <v-spacer></v-spacer>
                  <v-btn
                    class="mt-2 text-capitalize"
                    color="red"
                    :dark="addaddressform"
                    :disabled="!addaddressform"
                    @click="submitAddress"
                    >Submit</v-btn
                  >
                </v-card-actions>
              </v-card-text>
            </v-card>
          </keep-alive>
        </v-dialog>

        <v-divider class="mt-2"></v-divider>
        <v-row>
          <v-col
            lg="6"
            md="6"
            sm="12"
            v-for="(address, i) in addresses"
            :key="i"
          >
            <v-card min-width="250px">
              <v-card-title class="bg lighten-1 grey"
                >Address {{ i + 1 }}
                <v-spacer></v-spacer>
                <!--  <v-btn
                  color="grey"
                  small
                  @click="updateAddress(address.id)"
                  class="text-capitalize"
                  >Update<v-icon>edit_location_alt</v-icon></v-btn
                > --></v-card-title
              >
              <v-card-text>
                <v-list dense>
                  <v-list-item dense>
                    <v-list-item-content>
                      <v-list-item-title
                        >Country:
                        <span class="text-caption">{{
                          address.country
                        }}</span></v-list-item-title
                      >
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item dense>
                    <v-list-item-content>
                      <v-list-item-title
                        >City:
                        <span class="text-caption">{{
                          address.city
                        }}</span></v-list-item-title
                      >
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item dense>
                    <v-list-item-content>
                      <v-list-item-title
                        >Phone:
                        <span class="text-caption">{{
                          address.phone
                        }}</span></v-list-item-title
                      >
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item dense>
                    <v-list-item-content>
                      <v-list-item-title
                        >Address:
                        <span class="text-caption text-wrap">{{
                          address.address
                        }}</span></v-list-item-title
                      >
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item dense>
                    <v-list-item-content>
                      <v-list-item-title
                        >Postal Code:
                        <span class="text-caption">{{
                          address.postal_code
                        }}</span></v-list-item-title
                      >
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                <v-btn
                  small
                  class="mt-2 text-capitalize"
                  color="red"
                  dark
                  @click="deleteAddress(address.id)"
                  >delete</v-btn
                >
                <v-spacer></v-spacer>
                <v-btn
                  small
                  class="mt-2 text-capitalize"
                  color="red"
                  dark
                  @click="setDefault(address.id)"
                  v-if="address.set_default == 0"
                  >Set as Default</v-btn
                >
                <v-btn
                  small
                  class="mt-2 text-capitalize"
                  color="green"
                  dark
                  v-if="address.set_default == 1"
                  >Default Address <v-icon small>done</v-icon></v-btn
                >
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import ProfileContent from "../components/ProfileContent";
import Axios from "axios";
import { baseURI } from "../api/api";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      addaddressform: true,
      addnewaddress: false,
      addresslist: [],
      user: {
        postal_code: "",
        country: "",
        address: "",
        city: "",
        phone: "",
      },
      postalRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) =>
          v.length <= 6 ||
          "Postal code should be less than or equal to 6 characters",
      ],
      phoneRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) => v.length == 10 || "Phone number should be of 10 characters",
      ],
    };
  },
  methods: {
    fetchAddresses() {
      Axios.get(baseURI + "/addresses", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.addresslist = res.data.data;
      });
    },
    submitAddress() {
      if (this.$refs.form.validate()) {
        Axios.post(baseURI + "/addresses", this.user, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((res) => {
            if (res.status == 200) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
              this.addnewaddress = false;
            }
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      }
    },
    deleteAddress(addressId) {
      Swal.fire({
        title: "Do you really want to delete the item?",
        showDenyButton: true,
        showCancelButton: false,
        confirmButtonText: `Yes`,
        denyButtonText: `Cancel`,
      }).then((result) => {
        if (result.isConfirmed) {
          Axios.get(baseURI + "/addresses/destroy/" + addressId, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          })
            .then((res) => {
              if (res.status == 200) {
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 3000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "success",
                  title: res.data.message,
                });
              } else {
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 3000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "error",
                  title: "Something went wrong",
                });
              }
            })
            .catch(() => {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "error",
                title: "Something went wrong",
              });
            });
        } else if (result.isDenied) {
          Swal.fire("Deletion cancelled", "", "info");
        }
      });
    },
    setDefault(addressId) {
      Axios.get(baseURI + "/addresses/set_default/" + addressId, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      })
        .then((res) => {
          if (res.status == 200) {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: res.data.message,
            });
          } else {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          }
        })
        .catch(() => {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "error",
            title: "Something went wrong",
          });
        });
    },
  },
  components: {
    ProfileContent,
  },
  computed: {
    addresses() {
      this.fetchAddresses();
      return this.addresslist;
    },
  },
  mounted() {
    this.fetchAddresses();
  },
};
</script>
<style scoped>
.addresscol {
  min-width: 250px;
}
</style>
