<template>
  <div>
    <v-row justify="space-around">
      <v-col cols="4">
        <v-card elevation="5">
          <v-card-title class="bg lighten-2 grey">Reset Password</v-card-title>
          <v-card-text class="mt-3">
            <v-form ref="form" v-model="valid">
              <v-text-field
                :append-icon="showPass1 ? 'visibility' : 'visibility_off'"
                :type="showPass1 ? 'text' : 'password'"
                v-model="password"
                :rules="passRules"
                label="New Password"
                required
                outlined
                dense
                @click:append="showPass1 = !showPass1"
              ></v-text-field>

              <v-text-field
                :append-icon="showPass2 ? 'visibility' : 'visibility_off'"
                :type="showPass2 ? 'text' : 'password'"
                v-model="password_confirmation"
                :rules="[
                  this.password === this.password_confirmation ||
                    'Password must match',
                ]"
                label="Confirmation Password"
                required
                outlined
                dense
                @click:append="showPass2 = !showPass2"
              ></v-text-field>

              <div class="text-center">
                <v-btn
                  color="red"
                  :dark="valid"
                  :disabled="!valid"
                  small
                  class="text-capitalize mt-3"
                  @click="submitPassword"
                  >Create Password</v-btn
                >
              </div>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import Axios from "axios";
import { baseAuthURI } from "../api/api";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      valid: true,
      password: "",
      password_confirmation: "",
      showPass1: false,
      showPass2: false,
      passRules: [
        (v) => !!v || "Password is required",
        (v) =>
          (v && v.length >= 8) || "Password must be more than 8 characters",
      ],
    };
  },
  methods: {
    submitPassword() {
      if (this.$refs.form.validate()) {
        Axios.post(baseAuthURI + "/password-reset", {
          password: this.password,
          token: this.$route.params.token,
        })
          .then((res) => {
            if (res.status == 200) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
            }
          })
          .catch((err) => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: err.response.data.message,
            });
          });
      }
    },
  },
};
</script>