<template>
  <div>
    <v-row justify="space-around">
      <v-col cols="5">
        <v-card elevation="5">
          <v-card-title class="bg lighten-2 grey">Reset Password</v-card-title>
          <v-card-text class="mt-3">
            <v-form ref="form" v-model="valid">
              <v-text-field
                outlined
                label="Email Address"
                hint=" Enter your registered email to get the password reset link."
                v-model="resetEmail"
                persistent-hint
                :rules="emailRules"
              ></v-text-field>
              <v-btn
                color="red"
                :dark="valid"
                :disabled="!valid"
                small
                class="text-capitalize mt-3"
                @click="getResetPassLink"
                >Get Password Reset Link</v-btn
              >
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Axios from "axios";
import { baseAuthURI, hostAddress } from "../api/api";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      valid: true,
      resetEmail: "",
      emailStatus: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
    };
  },
  methods: {
    getResetPassLink() {
      if (this.$refs.form) {
        Axios.post(baseAuthURI + "/reset-password", {
          email: this.resetEmail,
          link: hostAddress + "/user/resetpassword/",
        })
          .then((res) => {
            if (res.status == 200) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
            }
          })
          .catch((err) => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: err.response.data.message,
            });
          });
      }
    },
  },
};
</script>