<template>
  <v-col lg="4" md="4" sm="4" v-if="userInfo != null">
    <v-card class="mx-auto" max-width="434">
      <v-img
        height="100%"
        src="https://cdn.vuetifyjs.com/images/cards/server-room.jpg"
      >
      </v-img>
      <v-row>
        <v-col class="pa-0" cols="12">
          <v-row>
            <v-col cols="4" class="avatar">
              <v-img
                v-if="userInfo.avatar != null"
                class="image"
                :src="imageURI + userInfo.avatar"
                max-width="150px"
                height="100%"
              ></v-img>
              <v-img
                v-else
                class="bg grey"
                max-width="150px"
                height="100%"
              ></v-img>
            </v-col>
            <v-col cols="7">
              <v-list-item three-line>
                <v-list-item-content>
                  <v-edit-dialog
                    :return-value.sync="userInfo.name"
                    @save="updateUserInfo"
                  >
                    <div>{{ userInfo.name }} <v-icon small>edit</v-icon></div>
                    <template v-slot:input>
                      <v-text-field
                        v-model="userInfo.name"
                        :rules="[max25chars]"
                        single-line
                        counter="25"
                        hint="Press enter to update"
                      ></v-text-field>
                    </template>
                  </v-edit-dialog>
                  <v-list-item-subtitle class="red--text">{{
                    userInfo.email
                  }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
          <div class="pa-3">
            <v-list-item
              v-for="(item, i) in profileContent"
              :key="i"
              :dark="getActiveTab == item.tabid ? true : false"
              @click="changeTab(item.label, item.link)"
              :class="getActiveTab == item.tabid ? 'bg red' : null"
            >
              <v-list-item-content>
                <v-list-item-title>{{ item.label }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </div>
        </v-col>
      </v-row>
    </v-card>
  </v-col>
</template>
<script>
import { baseImgURI, baseURI } from "../api/api";
import Axios from "axios";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      max25chars: (v) => v.length <= 25 || "Input too long!",
      imageURI: "",
      getActiveTab: "",
      profileContent: [
        {
          tabid: "account",
          label: "Profile",
          link: "/user/account",
        },
        {
          tabid: "address",
          label: "Addresses",
          link: "/user/address",
        },
        {
          tabid: "wishlist",
          label: "Wishlist",
          link: "/user/wishlist",
        },
        {
          tabid: "orders",
          label: "Order",
          link: "/user/orders",
        },
      ],
      userInfo: {
        name: "",
        email: "",
      },
    };
  },
  methods: {
    changeTab(name, link) {
      if (link != this.$route.path) {
        this.$router.replace(link);
      }
    },
    fetchUser() {
      Axios.get(baseURI + "/user/info/" + localStorage.getItem("user_id"), {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.userInfo.name = res.data.data[0].name;
        this.userInfo.email = res.data.data[0].email;
      });
    },
    updateUserInfo() {
      if (this.userInfo.name.length <= 25) {
        Axios.post(
          baseURI + "/user/info/update",
          {
            user_id: localStorage.getItem("user_id"),
            name: this.userInfo.name,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            },
          }
        ).then((res) => {
          this.fetchUser();
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 1500,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "success",
            title: res.data.message,
          });
        });
      }
    },
  },
  created() {
    this.getActiveTab = this.$route.path.split("/")[2];
    this.imageURI = baseImgURI;
    this.fetchUser();
  },
};
</script>
<style scoped>
.avatar {
  margin-top: -50px;
  margin-left: 20px;
}
</style>
