<script>
import Swal from "sweetalert2";

export default {
  props:['price'],
  data() {
    return {
      creditpanel: false,
      shipping: true,
      paypalpanel: false,
      radioGroup: 1,
      shippingMethod: [
        {
          name: "Cash On Delivery",
          deliveryTime: "2-4 days",
          handlingFee: 0,
        },
      ],
    };
  },
  
  methods: {
    changeTab(data, n) {
      if (this.shipping != "") {
        this.$emit("tab", data);
        this.$emit("e6", n);
        this.$emit("handlingFee", this.price);
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "info",
          title: "Payment method required",
        });
      }
    },
  },
};
</script>
