<template>
  <div>
    <v-row justify="space-around">
      <v-col lg="3" md="3" class="hidden-sm-and-down">
        <paginated-category-section></paginated-category-section>
      </v-col>
      <v-col lg="8" md="8" sm="12">
        <div class="mt-5">
          <v-breadcrumbs :items="items" small></v-breadcrumbs>
        </div>
       

        <div class="mt-2">
          <v-row  justify="space-around">
            <v-col lg="4" md="6" sm="6" class="d-flex justify-center" v-for="(item, i) in products" :key="i">
              <paginated-product-card :item="item"></paginated-product-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex justify-center">
              <div v-if="products == ''" class="mt-3">No products to show</div>
            </v-col>
          </v-row>
          <div class="d-flex justify-center mb-5" style="text-decoration: none">
            <v-pagination
              v-model="page"
              :length="length"
              prev-icon="keyboard_arrow_left"
              next-icon="keyboard_arrow_right"
              @input="getResults"
            ></v-pagination>
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import { baseURI } from "../api/api";
import Axios from "axios";
export default {
  props: ["category", "status"],
  data() {
    return {
      page: 1,
      length: null,
      sortingItems: ["Low To High", "High To Low"],
      items: [
        {
          text: "Home",
          href: "/",
        },
        {
          text: this.status,
        },
        {
          text: this.$route.params.slug,
        },
      ],
      products: [],
    };
  },
  methods: {
    async getResults(page = 1) {
      await Axios.get(
        baseURI + this.category + this.$route.params.slug + "?page=" + page
      ).then((response) => {
        this.products = response.data.data;
        this.length = parseInt(
          response.data.meta.last_page
        );
      });
    },
  },
  async mounted() {
    await this.getResults();
  },
};
</script>
