<template>
  <div>
    <v-container v-if="product != null && !fetchError">
      <v-list-item>
        <v-list-item-title>
          <v-breadcrumbs :items="items" divider=">>"></v-breadcrumbs
        ></v-list-item-title>
      </v-list-item>
      <v-row justify="space-around">
        <v-col lg="5" md="5" sm="12" class="px-0" style="min-width: 350px">
          <v-container>
            <v-row>
              <v-col cols="12">
                <image-magnifier
                  class="hidden-sm-and-down"
                  :src="imageURI + activeImage"
                  :zoom-src="imageURI + activeImage"
                  width="100%"
                  zoom-width="500"
                  zoom-height="300"
                ></image-magnifier>
                <image-zoom
                  style="width: 100%"
                  class="hidden-md-and-up"
                  :zoom-amount="1.5"
                  :regular="imageURI + activeImage"
                >
                </image-zoom>
              </v-col>
              <v-col cols="12">
                <div class="mt-2 mx-7">
                  <v-sheet elevation="0">
                    <v-slide-group v-model="model" show-arrows>
                      <v-slide-item
                        v-for="(item, n) in thumbnails"
                        :key="n"
                        style="max-width: 80px; max-height: 100px"
                      >
                        <v-card
                          :class="
                            activeImage == full_image[n]
                              ? 'redcard'
                              : 'greycard'
                          "
                          max-width="80px"
                          elevation="0"
                          @click="onThumbChange(n)"
                        >
                          <v-img
                            :src="imageURI + item"
                            width="70px"
                            height="70px"
                          ></v-img>
                        </v-card>
                      </v-slide-item>
                    </v-slide-group>
                  </v-sheet>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-col>
        <v-col lg="7" md="7" sm="12" style="min-width: 300px">
          <v-card-title>
            {{ product.name }}
          </v-card-title>
          <v-list-item>
            <v-list-item-subtitle>(Sku:{{ sku }})</v-list-item-subtitle>
          </v-list-item>
          <v-divider></v-divider>
          <div>
            <v-card-text>
              <v-list dense>
                <v-list-item dense>
                  <v-list-item-content>
                    <v-list-item-title>
                      Sold By:
                      <span
                        class="bg zOrange px-2 ml-2"
                        v-if="product.user != null"
                        >{{ product.user.shop_name }}</span
                      ></v-list-item-title
                    >
                  </v-list-item-content>
                  <v-list-item-content>
                    <v-list-item-title>
                      Rating:
                      <v-rating
                        color="zOrange"
                        x-small
                        dense
                        readonly
                        class="d-inline-block"
                        v-model="product.rating"
                      ></v-rating>
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title>
                      Brand:

                      <span
                        class="red--text d-inline-block"
                        @click="fetchBrandProducts"
                      >
                        <router-link to="#" class="red--text"
                          >brand</router-link
                        ></span
                      >
                    </v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-content>
                    <v-list-item-title>
                      Stock:
                      <span v-if="stock > 0" class="green--text d-inline-block"
                        >Available ({{ stock }})</span
                      >
                      <span v-else class="zOrange--text font-weight-bold"
                        >Out of stock</span
                      >
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-card-text>
            <v-divider></v-divider>
            <v-list>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>
                    Price:<span class="red--text ml-2 d-inline-block"
                      >{{ $currency }}.{{ variantPrice | twoDecimal }}</span
                    ></v-list-item-title
                  >
                </v-list-item-content>
                <v-list-item-content>
                  <v-list-item-title>
                    <span class="bg zOrange px-1">
                      {{ product.discount }}% less</span
                    >
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>
                  Quantity
                  <span>
                    <v-tooltip right>
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-bind="attrs"
                          v-on="on"
                          class="d-inline-block"
                          :disabled="stock <= 0 ? true : false"
                          outlined
                          dense
                          type="number"
                          color="zOrange"
                          style="width: 80px"
                          flat
                          v-model="quantity"
                          min="1"
                          :max="stock"
                        ></v-text-field>
                      </template>
                      <span>Available stock {{ stock }}</span>
                    </v-tooltip>
                  </span>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>
                  Price Range:
                  <span class="red--text"
                    >{{ $currency }}.({{ product.price_lower | twoDecimal }}-{{
                      product.price_higher | twoDecimal
                    }})</span
                  >
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list dense>
              <v-divider></v-divider>
              <v-list-item
                class="d-flex align-center"
                dense
                v-for="(choice, i) in product.choice_options"
                :key="i"
              >
                <v-btn-toggle>
                  <v-list-item-content
                    style="width: 100px"
                    class="font-weight-bold"
                  >
                    {{ choice.title }}
                  </v-list-item-content>
                  <v-list-item-content
                    style="width: 100px"
                    class="mr-1 ml-1"
                    v-for="(option, i) in choice.options"
                    :key="i"
                  >
                    <v-list-item-title>
                      <v-btn
                        outlined
                        tile
                        class="text-capitalize"
                        :dark="selected.includes(option) ? true : false"
                        :class="selected.includes(option) ? 'bg zOrange' : null"
                        @click="selectOption(choice.title, option)"
                      >
                        {{ option }}
                      </v-btn>
                    </v-list-item-title>
                  </v-list-item-content>
                </v-btn-toggle>
              </v-list-item>
            </v-list>

            <v-list>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>
                    <v-btn
                      tile
                      class="bg zOrange text-capitalize"
                      :dark="stock <= 0 ? false : true"
                      :disabled="stock <= 0 ? true : false"
                      min-width="150"
                      @click="addToCart"
                      >Add to Cart</v-btn
                    >
                  </v-list-item-title>
                </v-list-item-content>
                <v-list-item-content>
                  <v-list-item-title
                    ><v-btn
                      :disabled="stock <= 0 ? true : false"
                      tile
                      class="bg zOrange text-capitalize"
                      min-width="150"
                      :dark="stock <= 0 ? false : true"
                      @click="buyNow"
                      >Buy Now</v-btn
                    ></v-list-item-title
                  >
                </v-list-item-content>
              </v-list-item>
              <v-list-item dense>
                <v-list-item-content>
                  <v-list-item-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          v-on="on"
                          v-bind="attrs"
                          text
                          class="text-capitalize red--text"
                          min-width="150"
                          @click="addToWishList()"
                          >Add to WishList</v-btn
                        >
                      </template>
                      <span v-if="getWishlistProductId.includes(product.id)"
                        >Already in the Wishlist</span
                      >
                      <span v-else>Add to Wishlist</span>
                    </v-tooltip>
                  </v-list-item-title>
                </v-list-item-content>
                <v-list-item-content>
                  <v-list-item-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          v-on="on"
                          v-bind="attrs"
                          text
                          class="text-capitalize red--text"
                          min-width="150"
                          @click="addToCompare()"
                          >Add to Compare</v-btn
                        >
                      </template>
                      <span v-if="getProductIds.includes(product.id)"
                        >Already in the Compare List</span
                      >
                      <span v-else>Add to Compare List</span>
                    </v-tooltip>
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </div>
        </v-col>
        <!-- <v-col lg="3" md="12" sm="12">
          <delivery-option></delivery-option>
        </v-col> -->
        <v-list-item dense>
          <v-list-item-content>
            <v-list-item-title>
              Tags:
              <span
                class="red--text text-decoration-underline mr-1"
                v-for="tag in product.tags"
                :key="tag"
                >{{ tag }},</span
              >
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-row>
      <v-card elevation="0">
        <v-card-title>
          <v-btn
            v-for="(item, i) in tabs"
            :key="i"
            tile
            style="width: 150px"
            :class="
              activeTab == item
                ? 'bg zOrange text-capitalize'
                : 'bg white black--text text-capitalize'
            "
            dark
            elevation="3"
            @click="changeTab(item)"
            >{{ item }}</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-text>
          <description
            :activeTab="activeTab"
            :description="description"
          ></description>
          <!-- <additional-info :activeTab="activeTab"></additional-info> -->
          <product-review
            v-if="product.id != null"
            :activeTab="activeTab"
            :productID="product.id"
          ></product-review>
        </v-card-text>
      </v-card>
      <related-products :relatedProducts="relatedProducts"></related-products>
    </v-container>
    <div
      v-if="fetchError"
      class="font-weight-bold mt-5 pt-5 text-center"
      style="height: 100px"
    >
      No product found
    </div>
  </div>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
import Swal from "sweetalert2";
import RelatedProducts from "../components/RelatedProducts";
/* import DeliveryOption from "../components/DeliveryOption"; */
import Description from "../components/Description";
/* import AdditionalInfo from "../components/AdditionalInfo"; */
import ProductReview from "../components/ProductReview";
import { mapActions, mapMutations, mapGetters } from "vuex";
import imageZoom from "vue-image-zoomer";
import { ImageMagnifier } from "vue-image-magnifier";
import { EventBus } from "../eventbus/event-bus";

export default {
  data() {
    return {
      model: null,
      variantStatus: "",
      imageURI: "",
      fetchError: false,
      variantPrice: "",
      stock: "",
      sku: "",
      choices: {},
      variant: "",
      selected: [],
      product: {},
      relatedProducts: [],
      quantity: 1,
      choiceOptions: [],
      variations: [],
      activeTab: "Description",
      tabs: ["Description", "Reviews"],
      description: "",
      activeImage: "",
      full_image: [],

      items: [
        {
          text: "Home",
          disabled: false,
          href: "/",
        },
        {
          text: "Category",
          disabled: false,
          href: "/category/products/",
        },
        {
          text: "Sub-Category",
          disabled: false,
          href: "/category/sub-category/products/",
        },
      ],
      thumbnails: [],
    };
  },
  methods: {
    ...mapActions("Cart", ["addProductToCart"]),
    ...mapMutations("Compare", ["addToCompareList", "removeFromComparelist"]),
    ...mapMutations("Wishlist", ["storeIdToWishlist"]),
    setThumbnails(images) {
      this.thumbnails = images;
    },
    setBreadCrumbs() {
      let categorySlug = this.product.category.links.products.split("/")[7];
      let subcategorySlug = this.product.sub_category.links.products.split(
        "/"
      )[7];

      this.items[1].text = this.product.category.name;
      this.items[1].href = this.items[1].href + categorySlug;
      this.items[2].text = this.product.sub_category.name;
      this.items[2].href = this.items[2].href + subcategorySlug;
    },

    onThumbChange(index) {
      this.activeImage = this.full_image[index];
    },

    changeTab(data) {
      this.activeTab = data;
    },
    fetchBrandProducts() {
      this.$router.replace(
        "/brands/products/" + this.product.brand.links.products.split("/")[7]
      );
    },
    addToCompare() {
      if (this.getProductIds.includes(this.product.id)) {
        this.removeFromComparelist(this.product);
      } else {
        this.product.slug = this.$route.params.slug;
        this.addToCompareList(this.product);
      }
    },
    addToCart() {
      let product = {
        id: this.product.id,
        variant: this.variant,
        quantity: this.quantity,
      };

      if (localStorage.getItem("user_id")) {
        /* authentication add to cart */
        Axios.post(baseURI + "/carts/add", product, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((res) => {
            if (res.status == 200) {
              EventBus.$emit("addedToCart", true);
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 2000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
            }
          })
          .catch(() => {});
      } else {
        /* guest account add to cart*/

        let product = {
          id: this.product.id,
          product_id: this.product.id,
          product: {
            slug: this.$route.params.slug,
            name: this.product.name,
            image: this.activeImage,
          },
          variant: this.variant,
          variation: this.variant,
          quantity: this.quantity,
          price: this.variantPrice,
          shipping_type:
            this.product.shipping_type == null
              ? "home_delivery"
              : this.product.shipping_type,
          shipping_cost: this.product.shipping_cost,
          discount: this.product.discount,
          discount_type: this.product.discount_type,
          tax: this.product.tax,
          stock: this.stock,
          product_referral_code:
            this.product.product_referral_code &&
            this.product.product_referral_code != null
              ? this.product.product_referral_code
              : "dummycode",
        };
        this.addProductToCart(product);
        EventBus.$emit("addedToCart", true);
      }
    },

    selectOption(title, option) {
      this.choices[title] = option;
      this.computeVariations();
    },
    computeVariations() {
      let selectedChoice = [];

      if (this.choiceOptions.length == Object.keys(this.choices).length) {
        for (const key in this.choices) {
          if (Object.hasOwnProperty.call(this.choices, key)) {
            const element = this.choices[key];
            selectedChoice.push(element.replace(/\s+/g, ""));
          }
        }
        this.selected = selectedChoice;
        this.variant = selectedChoice.join("-");
        for (let i = 0; i < this.variations.length; i++) {
          if (this.variant == this.variations[i].variant) {
            this.stock = this.variations[i].qty;
            this.sku = this.variations[i].sku;
            this.variantPrice = this.variations[i].price;
            this.activeImage = this.variations[i].images.full_image[0];
            this.full_image = this.variations[i].images.full_image;
            this.setThumbnails(this.variations[i].images.thumb);
            return;
          } else if (this.variant != this.variations[i].variant) {
            this.stock = 0;
          }
        }
      }
    },

    addToWishList() {
      let userID = localStorage.getItem("user_id");
      let productID = this.product.id;

      const tokenId = localStorage.getItem("token_id");
      if (userID) {
        Axios.post(
          baseURI + "/wishlists/check-product",
          {
            user_id: userID,
            product_id: productID,
          },
          {
            headers: {
              Authorization: `Bearer ${tokenId}`,
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          }
        )
          .then((res) => {
            if (!res.data.is_in_wishlist) {
              Axios.post(
                baseURI + "/wishlists",
                {
                  user_id: userID,
                  product_id: productID,
                },
                {
                  headers: {
                    Authorization: `Bearer ${tokenId}`,
                    "Content-Type": "application/json",
                    Accept: "application/json",
                  },
                }
              )
                .then((res) => {
                  if (res.status == 201) {
                    this.storeIdToWishlist(productID);
                    const Toast = Swal.mixin({
                      toast: true,
                      position: "top-end",
                      showConfirmButton: false,
                      timer: 2000,
                      timerProgressBar: true,
                      didOpen: (toast) => {
                        toast.addEventListener("mouseenter", Swal.stopTimer);
                        toast.addEventListener("mouseleave", Swal.resumeTimer);
                      },
                    });

                    Toast.fire({
                      icon: "success",
                      title: res.data.message,
                    });
                  }
                })
                .catch(() => {});
            } else {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 2000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
            }
          })
          .catch(() => {});
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: "Authorization Required",
        });
      }
    },
    buyNow() {
      this.addToCart();
      this.$router.push("/user/cart");
    },
  },

  async created() {
    this.imageURI = baseImgURI;
    await Axios.get(baseURI + "/products/" + this.$route.params.slug)
      .then((res) => {
        this.product = res.data.data[0];
        this.activeImage = this.product.featured_image;
        this.variations = this.product.variations;
        this.choiceOptions = this.product.choice_options;
        this.description = this.product.description;
        this.stock = this.product.current_stock;

        setTimeout(() => {
          for (let i = 0; i < this.choiceOptions.length; i++) {
            this.choices[this.choiceOptions[i].title] = this.choiceOptions[
              i
            ].options[0];
          }
          let selectedChoice = [];
          if (this.variations != "") {
            if (this.choiceOptions.length == Object.keys(this.choices).length) {
              for (const key in this.choices) {
                if (Object.hasOwnProperty.call(this.choices, key)) {
                  const element = this.choices[key];
                  selectedChoice.push(element);
                }
              }
              this.selected = selectedChoice;
              this.variant = selectedChoice.join("-");
              for (let i = 0; i < this.variations.length; i++) {
                if (this.variant == this.variations[i].variant) {
                  this.stock = this.variations[i].qty;
                  this.sku = this.variations[i].sku;
                  this.variantPrice = this.variations[i].price;
                  this.activeImage = this.variations[i].images.full_image[0];
                  this.full_image = this.variations[i].images.full_image;
                  this.setThumbnails(this.variations[i].images.thumb);
                } else {
                  this.variant = this.variations[0].variant;
                  this.stock = this.variations[0].qty;
                  this.sku = this.variations[0].sku;
                  this.variantPrice = this.variations[0].price;
                  this.activeImage = this.variations[0].image;
                  this.full_image = this.variations[0].images.full_image;
                  this.setThumbnails(this.variations[0].images.thumb);
                }
              }
              this.selected = this.variant.split("-");
            }
          } else {
            this.variantPrice = this.product.price_higher;
          }
        }, 1000);

        if (this.product == undefined) {
          this.fetchError = true;
        }
        this.setBreadCrumbs();

        Axios.get(baseURI + "/products/related/" + this.$route.params.slug)
          .then((res) => {
            this.relatedProducts = res.data.data;
          })
          .catch(() => {});
      })
      .catch((err) => {
        if (err.response != undefined) {
          if (err.response.status == 500) {
            this.fetchError = true;
          }
        }
      });
  },
  computed: {
    ...mapGetters("Compare", ["getProductIds"]),
    ...mapGetters("Wishlist", ["getWishlistProductId"]),
  },

  components: {
    RelatedProducts,
    /* DeliveryOption, */
    Description,
    /* AdditionalInfo, */
    ProductReview,
    imageZoom,
    ImageMagnifier,
  },
};
</script>

<style scoped>
.thumbnail {
  padding: 50px;
  padding-top: 30px;
}
.greycard {
  border: 1px solid grey;
}
.redcard {
  border: 1px solid red;
}
.variant {
  margin-right: 32px;
}
.btn {
  height: 10px;
}
.v-btn--active {
  background-color: #f44333;
  color: white;
}
</style>
