<template>
  <div>
    <v-carousel
      cycle
      light
      height="400px"
      hide-delimiter-background
      show-arrows-on-hover
    >
      <v-carousel-item v-for="(slide, i) in sliders" :key="i">
        <v-sheet height="400px">
          <v-img height="400px" :src="imageURI + slide.photo"></v-img>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>

    <div class="mx-5 my-5">
      <v-img :src="imageURI+topBanner.photo" height="150px"></v-img>
    </div>
  </div>
</template>

<script>
import { baseImgURI, baseURI } from "../api/api";
import axios from "axios";
export default {
  props:['topBanner'],
  data() {
    return {
      sliders: [],
      imageURI: "",
    };
  },
  mounted() {
    this.imageURI = baseImgURI;
    axios
      .get(baseURI + "/sliders")
      .then((res) => {
        this.sliders = res.data.data;
      })
      .catch(() => {});

  
    

   
  },
};
</script>
