import Swal from "sweetalert2"
export default {
  namespaced: true,
  state: {
    cartItems: [],
  },
  getters: {
    getCartItems(state) {
      return state.cartItems;
    },
  },
  mutations: {
    pushProductToCart(state, product) {
      state.cartItems.push(product);
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 2000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "success",
        title: "Product added to cart successfully",
      });
    },
    incrementProductQty(state, product) {
      let indexOfproduct = state.cartItems.indexOf(product);
      state.cartItems.splice(indexOfproduct, 1, product);
    },
    changeQty(state, product) {
      let cartProduct = state.cartItems.find((prod) => prod.product_id === product.id);
      if (cartProduct) {
        if (product.quantity <= product.stock) {
          cartProduct.quantity = product.quantity;

          let indexOfproduct = state.cartItems.indexOf(cartProduct);
          state.cartItems.splice(indexOfproduct, 1, cartProduct);
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 2000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "success",
            title: "Cart Updated",
          });
        } else {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 2000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
          });

          Toast.fire({
            icon: "error",
            title: `Stock available ${product.stock}`,
          });
        }

      }
    },
    removeProduct(state, product) {
      let indexOfproduct = state.cartItems.indexOf(product);

      state.cartItems.splice(indexOfproduct, 1);
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 2000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "success",
        title: "Item Successfully removed from cart",
      });
    },
    clearCart(state) {
      state.cartItems = []
    }
  },
  actions: {
    addProductToCart({ state, commit }, product) {
      const cartProduct = state.cartItems.find(
        (prod) => prod.product_id === product.product_id
      );
      const cartProductVariant = state.cartItems.find(
        (prod) => prod.variation === product.variation
      );

      if (product.quantity <= product.stock) {
        if (!cartProduct) {
          commit("pushProductToCart", product);
        } else {
          if (!cartProductVariant) {
            commit("pushProductToCart", product);
          }
        }
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: `Stock available ${product.stock}`,
        });
      }


    },
  },
  modules: {},
};
