<template>
  <v-container>
    <!-- track  order -->
    <v-card elevation="5">
      <v-card-title class="bg grey lighten-2 mb-3">Track Order</v-card-title>
      <v-container>
        <keep-alive>
          <v-text-field
            label="Enter Order Code"
            color="red"
            outlined
            v-model="order_code"
          ></v-text-field>
        </keep-alive>
        <v-btn
          :loading="loader"
          :disabled="order_code == '' ? !loader : loader"
          :dark="order_code == '' ? loader : !loader"
          @click="trackOrder"
          color="red"
          class="text-capitalize"
          >Track Order
          <template v-slot:loader>
            <span class="custom-loader">
              <v-icon light>cached</v-icon>
            </span>
          </template></v-btn
        >
      </v-container>
    </v-card>
    <!-- order summary -->

    <v-card elevation="5" class="mt-5" v-if="myorders!=''">
      <v-card-title class="bg grey lighten-2 mb-3">Order Summary</v-card-title>
      <v-container>
        <!-- order table -->
        <tracked-data :orders="myorders" :shippingAddress="shippingAddress"></tracked-data>
      </v-container>
    </v-card>
  </v-container>
</template>
<script>
import axios from "axios";
import TrackedData from "../components/TrackedData";
import { baseURI } from "../api/api";
export default {
  data() {
    return {
      order_code: "",
      loader: false,
      myorders: [],
      shippingAddress:{}
    };
  },
  methods: {
    trackOrder() {
      this.myorders = []
      this.loader = true;
      setTimeout(() => {
        axios
          .get(`${baseURI}/track_your_order?order_code=${this.order_code}`)
          .then((res) => {
            this.myorders.push(res.data.order);
            this.shippingAddress = JSON.parse(res.data.order.shipping_address)
          });
        this.loader = false;
      }, 5000);
    },
  },
  components: {
    TrackedData,
  },
};
</script>
<style scoped>
.custom-loader {
  animation: loader 1s infinite;
  display: flex;
}
@-moz-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
