<template>
  <div v-if="products != ''" id="justforyou">
    <title-row :name="'/today/'">Just For You</title-row>
    <v-card class="sheet" outlined>
      <v-row>
        <v-col lg="4" md="4" sm="6" v-for="(item, i) in products" :key="i">
          <just-for-you-product-card :item="item"></just-for-you-product-card>
        </v-col>

        <v-col v-intersect="infiniteScrolling"> </v-col>
      </v-row>

      <v-row justify="space-around" v-if="loading">
        <v-col class="text-center">
          <v-progress-circular indeterminate color="red"></v-progress-circular>
        </v-col>
      </v-row>
      <v-row v-if="!loading">
        <v-col class="text-end">
          <v-speed-dial v-model="fab" bottom right>
            <template v-slot:activator>
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-on="on"
                    v-bind="attrs"
                    v-model="fab"
                    color="red"
                    dark
                    fab
                    href="#justforyou"
                  >
                    <v-icon> keyboard_arrow_up </v-icon>
                  </v-btn>
                </template>
                <span>GoTo Top</span>
              </v-tooltip>
            </template>
          </v-speed-dial>
        </v-col>
      </v-row>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import { baseURI } from "../api/api";
export default {
  name: "Posts",
  data() {
    return {
      products: [],
      page: 1,
      fab: true,
      bottom: true,
      right: true,
      loading: true
    };
  },

  computed: {
    url() {
      return baseURI + `/just-for-you?page=${this.page}`;
    },
  },

  created() {
    if (localStorage.getItem("user_id")) {
      this.fetchData();
    }
  },
  methods: {
    async fetchData() {
      await axios
        .get(this.url, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        })
        .then((response) => {
          if (response.data != "") {
            this.products = response.data[0].data;
          }
        });
    },
    infiniteScrolling() {
      setTimeout(() => {
        this.page++;
        axios
          .get(this.url, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            },
          })
          .then((response) => {
            if (response.data[0].data.length > 1) {
              if(this.page == response.data[0].last_page){
                this.loading = false
              }
              response.data[0].data.forEach((item) => this.products.push(item));
            }
          })
          .catch(() => {});
      }, 1000);
    },
  },
};
</script>
<style scoped>
.theme--light.v-card {
  background-color: #f5f5f5;
}
</style>
