/* eslint-disable */
import Axios from "axios";
import {
  baseAuthURI
} from "../../api/api";
import router from "../../router/index";
import {
  EventBus
} from "../../eventbus/event-bus"


import Swal from "sweetalert2"

export default {
  namespaced: true,
  state: {
    tokenId: null,
    userInfo: [],
    userShops: []
  },
  getters: {
    userInfo(state) {
      return state.userInfo;
    },
    getUserShops(state) {
      return state.userShops
    },
    isauthenticated(state) {
      return state.tokenId !== null;
    },

  },
  mutations: {
    authUser(state, userData) {
      state.tokenId = userData.token_id;
    },

    logoutUser(state) {
      state.tokenId = null;
      state.user = null;
      state.userShops = []
      state.userInfo = []
    },
    setUserInfo(state, payload) {
      state.userInfo = payload
    },
    setUsersShop(state, payload) {
      state.userShops = payload
    },
    setShop(state, payload) {

      if (payload.status == true) {
        state.userShops.push(payload.id)
      } else if (payload.status == false) {

        let indexOfShop = state.userShops.indexOf(payload.id);

        state.userShops.splice(indexOfShop, 1);
      }
    }
  },
  actions: {
    onSignUp({
      dispatch
    }, authData) {
      

      Axios.post(baseAuthURI + "/signup", {
          name: authData.name,
          email: authData.email,
          password: authData.password,
          phonenumber: authData.phonenumber,
          status: authData.status
        })
        .then((res) => {
          if (res.status == 201) {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: res.data.message,
            });

            location.reload()
          }
        })
        .catch((err) => {
          if (err.response.status == 422) {
            if (err.response.data.errors.email) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "error",
                title: err.response.data.errors.email[0],
              });
            }

          } else {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Registration failed: " + err.response.status + 'occurred',
            });
          }

        });
    },
    onSignIn({
      commit
    }, authData) {
      Axios.post(baseAuthURI + "/login", {
          email: authData.email,
          password: authData.password,
        })
        .then((res) => {
          if (res.status == 200) {
            commit("authUser", {
              token_id: res.data.access_token,
              user: res.data.user,
            });
            let user = res.data.user
            commit("setUserInfo", user);

            /* const expirationDate = res.data.expires_at; */
            let shop = res.data.user.storesFollowed
            commit('setUsersShop', shop)

            localStorage.setItem("token_id", res.data.access_token);
            localStorage.setItem("user_id", res.data.user.id)



            /* localStorage.setItem("expires_in", expirationDate); */
            /*  dispatch("logoutTimer", res.data.expires_at); */

            EventBus.$emit('loggedIn', true)

            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "User logged in successfully",
            });
            if (router.currentRoute.query.redirect_url) {
              router.replace(router.currentRoute.query.redirect_url)
            } else {
              router.replace('/')
            }
          }
        })
        .catch((err) => {
          if (err && err.response.status == 500) {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Server Error",
            });
          } else {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Authentication failed",
            });
          }

        });
    },

    logout({
      commit
    }) {

      commit("logoutUser");
      localStorage.removeItem("token_id");
      localStorage.removeItem("user_id");
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "success",
        title: "User is logged out",
      });
      router.replace("/user/login");
    },

    tryAutoLogin({
      commit
    }) {
      const token = localStorage.getItem("token_id");
      if (!token) {
        return;
      }
      /* const expirationDate = localStorage.getItem("expires_in");
      const now = new Date();
      if (now >= expirationDate) {
        return;
      }
 */
      commit("authUser", {
        token_id: token,
      });
    },
    /* logoutTimer({ commit }, expirationTime) {
      setTimeout(() => {
        commit("logoutUser");
      }, expirationTime * 1000);
    }, */
  },
};