export default {
    namespaced: true,
    state: {
        dummyUser:{
            name:'Customer',
            email:'email@email.com',
            country:'country',
            city:'city',
            phone:'+11111111111',
            address:'address',
            postal_code:'postal code'


        },
        billingAddress: null,
        shippingMethod: null,
        paymentMethod: null,
        paymentAmount: null,
        grandTotal: 0


    },
    getters: {
        getAddress(state) {
            return state.billingAddress
        },
        getPaymentMthd(state){
            return state.paymentMethod
        },
        getPaymentAmount(state){
            return state.paymentAmount
        },
        getGrandTotal(state){
            return state.grandTotal
        }

    },
    mutations: {
        storeBillingAddress(state, payload) {
            state.billingAddress = payload
        },
        payment(state, amount){
            state.paymentAmount = amount
        },
        storeGrandTotal(state, payload){
            state.grandTotal = payload
        },
        completeOrder(state){
            state.billingAddress = null,
            state.shippingMethod = null,
            state.paymentMethod = null,
            state.paymentAmount= null,
            state.grandTotal = 0
        }

    },
    actions: {
        setAddress({ commit }, data) {
            commit('storeBillingAddress', data)
        }
    },
    modules: {},
};
