<template>
  <div>
    <v-img :src="imageURI + banner.photo" height="150px"></v-img>

    <v-container>
      <div class="text-h5 font-weight-bold my-12">
        <v-row>
          <v-col cols="12" class="d-flex justify-center">
            <span>Follow Our Shop<v-divider class="red"></v-divider></span>
          </v-col>
        </v-row>
      </div>

      <v-row class="mt-5">
        <v-col lg="3" md="4" sm="6" v-for="(vendor, i) in vendors" :key="i">
          <v-hover v-slot="{ hover }">
            <v-card
              :elevation="hover ? 8 : 0"
              outlined
              :class="{ 'on-hover': hover }"
              class="mx-auto"
            >
              <router-link
                :to="'/vendor/visit/' + vendor.slug"
                class="text-decoration-none"
              >
                <v-img :src="imageURI + vendor.logo" height="150px"> </v-img>
                <v-card-text>
                  <div class="font-weight-bold black--text text-truncate">
                    {{ vendor.name }}
                  </div>
                </v-card-text></router-link
              >

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="red"
                  tile
                  dark
                  @click="follow(vendor.id)"
                  class="text-capitalize"
                >
                  <span
                    v-if="
                      getUserShops != null && getUserShops.includes(vendor.id)
                    "
                    >Following <v-icon color="green">done</v-icon></span
                  >
                  <span v-else>Follow</span>
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
import { mapGetters, mapMutations } from "vuex";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      followText: "follow",
      following: false,
      vendors: [],
      imageURI: "",
      banner:''
    };
  },
  methods: {
    ...mapMutations("CurrentUser", ["setShop"]),
    fetchShops() {
      Axios.get(baseURI + "/shops")
        .then((res) => {
          this.vendors = res.data.data;
        })
        .catch(() => {});
    },
    follow(shopId) {
      if (localStorage.getItem("user_id")) {
        Axios.get(baseURI + "/follow-shop/" + shopId, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        })
          .then((res) => {
            let payload = {
              id: shopId,
              status: res.data.status,
            };
            this.setShop(payload);
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "info",
              title: res.data.message,
            });
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: "Authorization required",
        });
      }
    },
  },
  created() {
    this.imageURI = baseImgURI;
    this.fetchShops();
    Axios
      .get(baseURI + "/banners")
      .then((res) => {
       this.banner = res.data.data[3]
      })
      .catch(() => {});
  },
  computed: {
    ...mapGetters("CurrentUser", ["getUserShops"]),
  },
};
</script>
<style scoped></style>
