<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title class="bg grey lighten-2"
            >Comparision <v-spacer></v-spacer>Reset Compare
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  class="ml-1"
                  color="red"
                  :dark="getCompareList.length == 0 ? false : true"
                  tile
                  text
                  x-small
                  v-on="on"
                  v-bind="attrs"
                  :disabled="getCompareList.length == 0 ? true : false"
                  @click="clearCompareList"
                  ><v-icon>remove_circle</v-icon></v-btn
                >
              </template>
              <span>Clear compare list</span>
            </v-tooltip>
          </v-card-title>
          <v-card-text v-if="getCompareList != ''">
            <v-container>
              <v-card elevation="0">
                <v-row>
                  <v-col cols="12">
                    <v-row>
                      <v-col
                        v-for="(item, i) in getCompareList"
                        :key="i"
                        v-show="i < 5"
                        lg="3"
                        md="3"
                        sm="6"
                      >
                        <v-card elevation="0" outlined class="mx-2">
                          <div class="text-end">
                            <v-btn x-small text @click="removeProduct(item)"
                              ><v-icon color="red">clear</v-icon></v-btn
                            >
                          </div>
                          <router-link
                            class="text-decoration-none black--text"
                            :to="'/single/product/' + item.slug"
                          >
                            <v-img
                              :src="imageURI + item.featured_image"
                              height="150px"
                            ></v-img>
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-card-title
                                  class="text-subtitle-2 d-inline-block text-truncate"
                                  style="max-width: 240px"
                                  v-bind="attrs"
                                  v-on="on"
                                  >{{ item.name }}
                                </v-card-title>
                              </template>
                              <span>{{ item.name }}</span>
                            </v-tooltip>
                          </router-link>
                          <v-list dense>
                            <v-list-item dense>
                              <v-list-item-content>
                                <v-list-item-subtitle
                                  ><span class="font-weight-bold">Category</span
                                  >: {{ item.category }}</v-list-item-subtitle
                                >
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                          <v-list nav dense>
                            <v-list-item dense>
                              <v-list-item-content
                                ><v-list-item-title class="green--text"
                                  >{{ $currency }}.{{
                                    item.base_discounted_price ||
                                    item.purchase_price | twoDecimal
                                  }}</v-list-item-title
                                ></v-list-item-content
                              >
                              <v-list-item-content
                                ><v-list-item-title
                                  class="text-decoration-line-through zOrange--text text-end"
                                  >{{ $currency }}.{{
                                    item.base_price ||
                                    item.unit_price | twoDecimal
                                  }}</v-list-item-title
                                ></v-list-item-content
                              >
                            </v-list-item>
                            <v-list-item dense>
                              <v-list-item-icon
                                ><v-tooltip bottom>
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-hover v-slot="{ hover, click }">
                                      <v-btn
                                        x-small
                                        text
                                        v-on="on"
                                        v-bind="attrs"
                                        @click="addToWishlist(item.id)"
                                        :color="hover || click ? 'white' : null"
                                      >
                                        <v-icon
                                          :color="
                                            getWishlistProductId.includes(
                                              item.id
                                            )
                                              ? 'zOrange'
                                              : 'grey'
                                          "
                                          >favorite</v-icon
                                        >
                                      </v-btn>
                                    </v-hover>
                                  </template>
                                  <span
                                    v-if="
                                      getWishlistProductId.includes(item.id)
                                    "
                                    >Already in the Wishlist</span
                                  >
                                  <span v-else>Add to Wishlist</span>
                                </v-tooltip></v-list-item-icon
                              >
                              <v-list-item-content>
                                <v-list-item-subtitle
                                  ><v-rating
                                    length="5"
                                    color="red"
                                    v-model="rating"
                                    x-small
                                  ></v-rating
                                ></v-list-item-subtitle>
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                          <v-list dense>
                            <v-list-item dense>
                              <v-list-item-content>
                                <v-list-item-subtitle
                                  >Sales: {{ item.sales }}</v-list-item-subtitle
                                >
                              </v-list-item-content>
                              <v-list-item-content>
                                <v-list-item-subtitle
                                  >Discount:
                                  {{ item.discount }}</v-list-item-subtitle
                                >
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                        </v-card>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card>
            </v-container>
          </v-card-text>
          <v-card-text v-else>
            <div class="text-center text-subtitle-2 mt-5">
              No compare products
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
import { baseImgURI, baseURI } from "../api/api";
import Swal from "sweetalert2";
import Axios from "axios";
export default {
  data() {
    return {
      rating: 4,
      imageURI: "",
    };
  },
  methods: {
    ...mapMutations("Compare", ["removeFromComparelist", "clearCompareList"]),
    ...mapMutations("Wishlist", ["storeIdToWishlist", "removeIdFromWishlist"]),
    addToWishlist(id) {
      let userID = localStorage.getItem("user_id");
      let productID = id;

      const tokenId = localStorage.getItem("token_id");
      if (userID) {
        Axios.post(
          baseURI + "/wishlists/check-product",
          {
            user_id: userID,
            product_id: productID,
          },
          {
            headers: {
              Authorization: `Bearer ${tokenId}`,
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          }
        )
          .then((res) => {
            if (!res.data.is_in_wishlist) {
              Axios.post(
                baseURI + "/wishlists",
                {
                  user_id: userID,
                  product_id: productID,
                },
                {
                  headers: {
                    Authorization: `Bearer ${tokenId}`,
                    "Content-Type": "application/json",
                    Accept: "application/json",
                  },
                }
              )
                .then((res) => {
                  this.storeIdToWishlist(productID);
                  const Toast = Swal.mixin({
                    toast: true,
                    position: "top-end",
                    showConfirmButton: false,
                    timer: 2000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                      toast.addEventListener("mouseenter", Swal.stopTimer);
                      toast.addEventListener("mouseleave", Swal.resumeTimer);
                    },
                  });

                  Toast.fire({
                    icon: "success",
                    title: res.data.message,
                  });
                })
                .catch(() => {});
            } else {
              Axios.delete(baseURI + "/wishlists/" + res.data.wishlist_id, {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token_id")}`,
                },
              }).then((res) => {
                this.removeIdFromWishlist(productID);
                const Toast = Swal.mixin({
                  toast: true,
                  position: "top-end",
                  showConfirmButton: false,
                  timer: 2000,
                  timerProgressBar: true,
                  didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                  },
                });

                Toast.fire({
                  icon: "success",
                  title: res.data.message,
                });
              });
            }
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      } else {
        this.$router.replace("/user/login");
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "success",
          title: "Authorization Required",
        });
      }
    },
    removeProduct(item) {
      this.removeFromComparelist(item);
    },
  },
  mounted() {
    this.imageURI = baseImgURI;
  },
  computed: {
    ...mapGetters("Compare", ["getCompareList"]),
    ...mapGetters("Wishlist", ["getWishlistProductId"]),
  },
};
</script>
