<template>
  <v-card elevation="0" class="mt-10" min-width="400px">
    <v-form @submit.prevent="formSearch">
      <v-autocomplete
        class="d-inline-block"
        v-model="model"
        :items="items"
        :search-input.sync="search"
        color="zOrange"
        item-text="name"
        item-value="name"
        :loading="isLoading"
        :menu-props="{ closeOnContentClick: true, closeOnClick: true }"
        hide-details
        placeholder="Start typing to Search"
        append-icon="search"
        return-object
        clearable
        outlined
        no-filter
        eager
        full-width
        @click:append="formSearch"
        style="width: 100%"
      >
        <template v-slot:no-data>
          <v-progress-linear
            color="red"
            rounded
            indeterminate
            v-if="!no_data"
          ></v-progress-linear>
          <span v-if="keywords != ''">
            <v-list-item-title class="subtitle-2 mx-2"
              >#Popular Suggestions</v-list-item-title
            >
            <v-list-item
              dense
              v-for="(key, i) in keywords"
              :key="i"
              :to="`/search/products?q=${key}`"
            >
              <v-list-item-content>
                <v-list-item-subtitle v-html="key"></v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </span>
          <span v-if="subsubcategories != ''">
            <v-list-item-title class="subtitle-2 mx-2"
              >#Category Suggestions</v-list-item-title
            >
            <v-list-item
              dense
              v-for="(sug, i) in subsubcategories"
              :key="i"
              :to="`/category/sub-sub-category/products/${sug.slug}`"
            >
              <v-list-item-content>
                <v-list-item-subtitle v-html="sug.name"></v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </span>
          <span v-if="shops != ''">
            <v-list-item-title class="subtitle-2 mx-2"
              >#Shops</v-list-item-title
            >
            <v-list-item
              dense
              v-for="(shop, i) in shops"
              :key="i"
              :to="`/vendor/visit/${shop.slug}`"
            >
              <v-list-item-content>
                <v-list-item-subtitle v-html="shop.name"></v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </span>
        </template>
        <!-- <template v-slot:progress>
          <v-progress-linear color="green" rounded bottom indeterminate></v-progress-linear>
        </template> -->

        <template v-slot:item="{ item }" style="width: 400px">
          <v-list-item dense :to="`/single/product/${item.slug}`">
            <v-list-item-avatar>
              <v-img :src="imageURI + item.thumbnail_img"></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
              <v-list-item-subtitle>
                <span class="green--text mr-5"
                  >{{ $currency }} {{ item.purchase_price }}</span
                ><span class="red--text text-decoration-line-through"
                  >{{ $currency }} {{ item.unit_price }}</span
                ></v-list-item-subtitle
              >
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-autocomplete>
    </v-form>
  </v-card>
</template>
<script>
import axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
export default {
  data: () => ({
    imageURI: "",
    products: [],
    keywords: [],
    model: null,
    search: null,
    isLoading: false,
    subsubcategories: [],
    shops: [],
    no_data: false,
  }),
  methods: {
    formSearch() {
      this.$router
        .replace({ name: "search", query: { q: this.search } })
        .catch(() => {});
    },
  },

  computed: {
    items() {
      return this.products.map((product) => {
        return Object.assign({}, product);
      });
    },
  },
  watch: {
    search() {
      this.products = [];
      this.subsubcategories = [];
      this.shops = [];

      this.isLoading = true;
      axios
        .post(baseURI + "/ajax-search?search=" + this.search)
        .then((res) => {
          if (res.data != 0) {
            this.no_data = false;
            this.products = res.data.products;
            this.keywords = res.data.keywords;
            this.subsubcategories = res.data.subsubcategories;
            this.shops = res.data.shops;
          } else {
            this.no_data = true;
          }
        })
        .catch(() => {})
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
  mounted() {
    this.imageURI = baseImgURI;
  },
};
</script>
<style scoped>
/deep/ .v-autocomplete {
  width: 400px;
}
</style>
