<template>
  <v-hover v-slot="{ hover }">
    <v-card
      :elevation="hover ? 8 : 0"
      outlined
      :class="{ 'on-hover': hover }"
      class="ma-2 pcard"
      height="320"
      width="330"
    >
      <router-link
        class="text-decoration-none black--text"
        :to="'/single/product/' + item.slug"
      >
        <v-img
          :src="imageURI + item.featured_image"
          height="150"
          width="330"
        ></v-img>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-card-title
              class="text-subtitle-2 d-inline-block text-truncate"
              style="max-width: 300px"
              v-bind="attrs"
              v-on="on"
              >{{ item.name }}
            </v-card-title>
          </template>
          <span>{{ item.name }}</span>
        </v-tooltip>
      </router-link>

      <v-list nav dense>
        <v-list-item dense class="text-center">
          <v-list-item-content
            ><v-list-item-title class="green--text"
              >{{ $currency }}.{{
                item.base_discounted_price | twoDecimal
              }}</v-list-item-title
            ></v-list-item-content
          >
          <v-list-item-content
            ><v-list-item-title
              class="text-decoration-line-through zOrange--text"
              >{{ $currency }}.{{
                item.base_price | twoDecimal
              }}</v-list-item-title
            ></v-list-item-content
          >
        </v-list-item>
        <v-list-item dense>
          <v-list-item-content
            ><v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-hover v-slot="{ hover, click }">
                  <v-btn
                    x-small
                    text
                    v-on="on"
                    v-bind="attrs"
                    @click="addToWishlist(item.id)"
                    :color="hover || click ? 'white' : null"
                  >
                    <v-icon
                      :color="
                        getWishlistProductId.includes(item.id)
                          ? 'zOrange'
                          : 'grey'
                      "
                      >favorite</v-icon
                    >
                  </v-btn>
                </v-hover>
              </template>
              <span v-if="getWishlistProductId.includes(item.id)"
                >Already in the Wishlist</span
              >
              <span v-else>Add to Wishlist</span>
            </v-tooltip></v-list-item-content
          >

          <v-list-item-content
            ><v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-hover v-slot="{ hover, click }">
                  <v-btn
                    width="5px"
                    x-small
                    text
                    v-bind="attrs"
                    v-on="on"
                    @click="addToCompare(item)"
                    :color="hover || click ? 'white' : null"
                  >
                    <v-icon
                      :color="
                        getProductIds.includes(item.id) ? 'zOrange' : 'grey'
                      "
                      >compare_arrows</v-icon
                    >
                  </v-btn>
                </v-hover>
              </template>
              <span v-if="getProductIds.includes(item.id)"
                >Already in the Compare List</span
              >
              <span v-else>Add to Compare List</span>
            </v-tooltip></v-list-item-content
          >
        </v-list-item>
      </v-list>
    </v-card>
  </v-hover>
</template>
<script>
import { baseImgURI } from "../api/api";
import Axios from "axios";
import { baseURI } from "../api/api";
import Swal from "sweetalert2";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  props: ["item"],
  data() {
    return {
      imageURI: "",
      prating: 3,
      wishlist: [],
    };
  },
  methods: {
    ...mapActions("Compare", ["addToCompareList"]),
    ...mapMutations("Wishlist", ["storeIdToWishlist", "removeIdFromWishlist"]),
    ...mapMutations("Compare", ["removeFromComparelist"]),
    addToWishlist(id) {
      let userID = localStorage.getItem("user_id");
      let productID = id;

      const tokenId = localStorage.getItem("token_id");
      if (userID) {
        Axios.post(
          baseURI + "/wishlists/check-product",
          {
            user_id: userID,
            product_id: productID,
          },
          {
            headers: {
              Authorization: `Bearer ${tokenId}`,
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          }
        )
          .then((res) => {
            if (!res.data.is_in_wishlist) {
              Axios.post(
                baseURI + "/wishlists",
                {
                  user_id: userID,
                  product_id: productID,
                },
                {
                  headers: {
                    Authorization: `Bearer ${tokenId}`,
                    "Content-Type": "application/json",
                    Accept: "application/json",
                  },
                }
              )
                .then((res) => {
                  if (res.status == 201) {
                    this.storeIdToWishlist(productID);
                    const Toast = Swal.mixin({
                      toast: true,
                      position: "top-end",
                      showConfirmButton: false,
                      timer: 3000,
                      timerProgressBar: true,
                      didOpen: (toast) => {
                        toast.addEventListener("mouseenter", Swal.stopTimer);
                        toast.addEventListener("mouseleave", Swal.resumeTimer);
                      },
                    });

                    Toast.fire({
                      icon: "success",
                      title: res.data.message,
                    });
                  }
                })
                .catch(() => {});
            } else {
              Axios.delete(baseURI + "/wishlists/" + res.data.wishlist_id, {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token_id")}`,
                },
              }).then((res) => {
                if (res.status == 200) {
                  this.removeIdFromWishlist(productID);
                  const Toast = Swal.mixin({
                    toast: true,
                    position: "top-end",
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                      toast.addEventListener("mouseenter", Swal.stopTimer);
                      toast.addEventListener("mouseleave", Swal.resumeTimer);
                    },
                  });

                  Toast.fire({
                    icon: "success",
                    title: res.data.message,
                  });
                }
              });
            }
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: "Authorization Required",
        });
      }
    },

    addToCompare(item) {
      if (this.getProductIds.includes(item.id)) {
        this.removeFromComparelist(item);
      } else {
        this.addToCompareList(item);
      }
    },
  },
  mounted() {
    this.imageURI = baseImgURI;
  },
  computed: {
    ...mapGetters("Wishlist", ["getWishlistProductId"]),
    ...mapGetters("Compare", ["getProductIds"]),
  },
};
</script>
