<template>
  <div v-if="bestSellers != ''">
    <title-row status="bestseller">Best Sellings</title-row>
    <v-sheet class="mt-5 sheet">
      <v-divider></v-divider>
      <v-slide-group v-model="model" show-arrows>
        <v-slide-item v-for="(item, i) in bestSellers" :key="i">
          <v-card class="ma-3" height="100" width="200">
            <v-list-item dense three-line class="px-3" :to="{name:'singleProduct',params:{slug:item.slug}}">
              <v-list-item-avatar tile size="70" color="grey"
                ><v-img :src="imageURI+item.thumbnail_image"></v-img
              ></v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title
                  @click="singleProduct(item.links.details)"
                  class="text-caption mb-1 text-truncate"
                  style="max-width: 90px"
                >
                  <router-link to="#" class="text-decoration-none black--text">{{
                    item.name
                  }}</router-link></v-list-item-title
                >

                <v-list-item-subtitle
                  ><v-rating
                    readonly
                    dense
                    color="red"
                    x-small
                    v-model="item.rating"
                  ></v-rating
                ></v-list-item-subtitle>
                <v-list-item-subtitle class="green--text">
                  {{$currency}}.{{item.base_discounted_price | twoDecimal}}
                </v-list-item-subtitle>
                
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-slide-item>
      </v-slide-group>
    </v-sheet>
  </div>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
export default {
  data: () => ({
    model: null,
    bestSellers: [],
    imageURI:''
  }),
  mounted() {
    this.imageURI = baseImgURI
    Axios.get(baseURI + "/products/best-seller").then((res) => {
      this.bestSellers = res.data.data;
    });
  },
  methods: {
    singleProduct(link) {
      this.$router.replace("/single/product/" + link.split("/")[6]);
    },
  },
};
</script>
