<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="3" v-for="(pkg, i) in packages" :key="i">
          <v-card class="pa-5" height="350" elevation="0" outlined width="450">
            <v-img :src="imageURI + pkg.logo" height="225" width="225"></v-img>
            <v-list-item three-line class="text-center">
              <v-list-item-content>
                <v-list-item-title class="text-subtitle-2 font-weight-bold">{{
                  pkg.name
                }}</v-list-item-title>
                <v-spacer></v-spacer>
                <v-list-item-subtitle
                  >{{ $currency }}.{{ pkg.amount }}</v-list-item-subtitle
                >
                <v-list-item-title class="text-subtitle-1"
                  >Product Upload:
                  <span class="font-weight-bold">{{
                    pkg.product_upload
                  }}</span></v-list-item-title
                >
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
export default {
  data() {
    return {
      packages: [],
      imageURI: "",
    };
  },
  created() {
    this.imageURI = baseImgURI;
    Axios.get(baseURI + "/customer_package")
      .then((res) => {
        this.packages = res.data;
      })
      .catch(() => {});
  },
};
</script>
