<template>
  <div>
    <v-carousel cycle height="150" hide-delimiters show-arrows-on-hover>
      <v-carousel-item v-for="(slide, i) in vendor.sliders" :key="i">
        <v-sheet>
          <v-img :src="imageURI + slide"> </v-img>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
    <v-container>
      <v-row>
        <v-card dark elevation="0" class="bg blue-grey darken-4 my-5">
          <v-list-item three-line>
            <v-list-item-avatar size="70" class="grey my-10"
              ><v-img
                v-if="vendor.logo != null"
                :src="imageURI + vendor.logo"
              ></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>
                <v-card-title>
                  {{ vendor.name }}
                  <v-icon color="green" small>check_circle</v-icon>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="red"
                    tile
                    dark
                    @click="follow(vendor.id)"
                    class="text-capitalize"
                  >
                    <span
                      v-if="
                        getUserShops != null && getUserShops.includes(vendor.id)
                      "
                      >Following <v-icon color="green">done</v-icon></span
                    >
                    <span v-else>Follow</span>
                  </v-btn>
                </v-card-title>
              </v-list-item-title>
              <v-list-item-subtitle class="mx-4"
                ><v-rating readonly x-small dense></v-rating
              ></v-list-item-subtitle>
              <v-list-item-subtitle class="mx-4">{{
                vendor.address
              }}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-card>
        <v-spacer></v-spacer>
      </v-row>

      <v-row class="mt-5">
        <v-btn
          v-for="(item, i) in shopFeatures"
          :key="i"
          :color="feature == item.title ? 'red white--text' : null"
          class="text-capitalize"
          tile
          @click="changeFeature(item)"
          >{{ item.title }}</v-btn
        >

        <v-spacer></v-spacer>
      </v-row>

      <v-row class="mt-5">
        <!-- store home -->
        <v-col
          cols="12"
          class="d-flex justify-center"
          v-if="feature == 'Store Home'"
        >
          <v-row>
            <v-col
              cols="12"
              class="text-h6 font-weight-bold d-flex justify-center"
            >
              <span>Featured Products<v-divider class="red"></v-divider></span>
            </v-col>
            <v-col
              lg="3"
              md="4"
              sm="6"
              class="mt-3"
              v-for="(item, i) in featuredProducts"
              :key="i"
              v-show="feature == 'Store Home'"
            >
              <product-card :item="item"></product-card>
            </v-col>
          </v-row>
        </v-col>

        <v-col v-show="feature == 'Store Home'" v-if="featuredProducts == ''">
          <div class="text-center">No featured Products</div>
        </v-col>
      </v-row>
      <v-row class="mb-5" v-if="newProducts != ''">
        <v-col cols="12" v-if="feature == 'Store Home'">
          <span class="font-weight-bold">New Arrival Products</span>
          <v-divider></v-divider>
          <v-row>
            <v-col
              lg="3"
              md="4"
              sm="6"
              v-for="item in newProducts"
              :key="item.title"
            >
              <product-card :item="item"></product-card>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" v-else>
          {{ feature }}
          <v-divider></v-divider>
        </v-col>

        <v-col
          lg="3"
          md="4"
          sm="6"
          v-for="item in shopProducts"
          :key="item.title"
          v-show="feature == 'Top Selling'"
        >
          <product-card :item="item"></product-card>
        </v-col>
        <v-col
          lg="3"
          md="4"
          sm="6"
          v-for="item in shopProducts"
          :key="item.title"
          v-show="feature == 'All Products'"
        >
          <product-card :item="item"></product-card>
        </v-col>
        <v-col
          lg="3"
          md="4"
          sm="6"
          v-for="item in shopProducts"
          :key="item.title"
          v-show="feature == 'Top Brands'"
        >
          <product-card :item="item"></product-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";
import { mapGetters, mapMutations } from "vuex";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      vendor: {},
      imageURI: "",
      feature: "",
      shopFeatures: [
        {
          title: "Store Home",
          link: "",
        },
        {
          title: "Top Selling",
          link: "",
        },
        {
          title: "All Products",
          link: "",
        },
        {
          title: "Top Brands",
          link: "",
        },
      ],
      featuredProducts: [],
      newProducts: [],
      shopProducts: [],
    };
  },
  methods: {
    ...mapMutations("CurrentUser", ["setShop"]),
    changeFeature(item) {
      this.feature = item.title;
      /* fetch products acc to feature */
      Axios.get(item.link)
        .then((res) => {
          this.shopProducts = res.data.data;
        })
        .catch(() => {});
    },
    follow(shopId) {
      if (localStorage.getItem("user_id")) {
        Axios.get(baseURI + "/follow-shop/" + shopId, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        })
          .then((res) => {
            let payload = {
              id: shopId,
              status: res.data.status,
            };
            this.setShop(payload);
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "info",
              title: res.data.message,
            });
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      } else {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 2000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "error",
          title: "Authorization required",
        });
      }
    },
  },
  mounted() {
    this.feature = this.shopFeatures[0].title;
  },
  created() {
    this.imageURI = baseImgURI;
    Axios.get(baseURI + "/shops/details/" + this.$route.params.slug)
      .then((res) => {
        this.vendor = res.data.data[0];
        this.shopFeatures[1].link = this.vendor.links.top;
        this.shopFeatures[2].link = this.vendor.links.all;
        this.shopFeatures[3].link = this.vendor.links.brands;
        /* fetch featured and new products of vendor */
        let featuredProdAPI = Axios.get(this.vendor.links.featured);
        let newProdAPI = Axios.get(this.vendor.links.new);
        Axios.all([featuredProdAPI, newProdAPI]).then(
          Axios.spread((...res) => {
            this.featuredProducts = res[0].data.data;
            this.newProducts = res[1].data.data;
          })
        );
      })
      .catch(() => {});
  },
  computed: {
    ...mapGetters("CurrentUser", ["getUserShops"]),
  },
};
</script>
