<template>
  <v-container>
    <v-row>
      <ProfileContent />
      <!-- if activeTab is account(profile) -->
      <v-col lg="8" md="8" sm="8">
        <v-dialog v-model="changepass" max-width="400px">
          <template v-slot:activator="{ on, attrs }">
            <v-card-title
              ><v-spacer></v-spacer>
              <v-btn
                class="text-capitalize"
                small
                color="red"
                dark
                v-bind="attrs"
                v-on="on"
              >
                Change Password
              </v-btn></v-card-title
            >
          </template>
          <v-card>
            <v-card-title>
              <span class="headline red--text">Change Password</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-form v-model="changepasswordform" ref="cform">
                      <v-text-field
                        :append-icon="
                          showoldPass ? 'visibility' : 'visibility_off'
                        "
                        :type="showoldPass ? 'text' : 'password'"
                        v-model="oldpassword"
                        label="Old Password"
                        required
                        dense
                        single-line
                        color="red"
                        @click:append="showoldPass = !showoldPass"
                        :rules="[(v) => !!v || 'Password is required']"
                      ></v-text-field>
                      <v-text-field
                        :append-icon="
                          shownewPass ? 'visibility' : 'visibility_off'
                        "
                        :type="shownewPass ? 'text' : 'password'"
                        label="New Password"
                        v-model="newpassword"
                        required
                        dense
                        single-line
                        color="red"
                        @click:append="shownewPass = !shownewPass"
                        :rules="passRules"
                      ></v-text-field>
                      <v-text-field
                        :append-icon="
                          showconfirmPass ? 'visibility' : 'visibility_off'
                        "
                        :type="showconfirmPass ? 'text' : 'password'"
                        label="Retype Password"
                        v-model="confirmpassword"
                        required
                        dense
                        single-line
                        color="red"
                        @click:append="showconfirmPass = !showconfirmPass"
                        :rules="[
                          this.newpassword === this.confirmpassword ||
                            'Password must match',
                        ]"
                      ></v-text-field>
                    </v-form>
                    <v-row>
                      <v-col>
                        <v-btn
                          color="red"
                          dark
                          small
                          class="text-capitalize mt-2"
                          @click="changepass = false"
                          >Cancel</v-btn
                        >
                      </v-col>
                      <v-col class="d-flex justify-end">
                        <v-btn
                          color="red"
                          small
                          :disabled="!changepasswordform"
                          :dark="changepasswordform"
                          class="text-capitalize mt-2"
                          @click="changePassword"
                          >Confirm</v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
        </v-dialog>
        <v-form>
          <v-container>
            <v-divider class="mt-5 mb-5"></v-divider>
            <v-card elevation="0">
              <v-card-title>User Address</v-card-title>
              <v-card-text>
                <v-form ref="aform" v-model="updateaddressform">
                  <v-row>
                    <v-col lg="6" md="6" sm="6">
                      Country
                      <v-text-field
                        v-model="user.country"
                        required
                        outlined
                        dense
                        color="red"
                        class="mt-2"
                        :rules="[!!this.user.country || 'Country is required']"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="6" md="6" sm="6">
                      City
                      <v-text-field
                        v-model="user.city"
                        required
                        outlined
                        dense
                        color="red"
                        class="mt-2"
                        :rules="[!!this.user.city || 'City is required']"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="6" md="6" sm="6">
                      Phone
                      <v-text-field
                        v-model="user.phone"
                        required
                        outlined
                        dense
                        color="red"
                        class="mt-2"
                        :rules="phoneRule"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="6" md="6" sm="6">
                      Postal Code
                      <v-text-field
                        v-model="user.postal_code"
                        required
                        outlined
                        dense
                        color="red"
                        class="mt-2"
                        :rules="postalRule"
                      ></v-text-field>
                    </v-col>
                    <v-col lg="12" md="12" sm="12">
                      Address
                      <v-text-field
                        v-model="user.address"
                        required
                        outlined
                        dense
                        color="red"
                        class="mt-2"
                        :rules="[!!this.user.address || 'Address is required']"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  @click="updateUserAddress"
                  small
                  class="text-capitalize"
                  color="red"
                  :dark="updateaddressform"
                  :disabled="!updateaddressform"
                  >Update Address</v-btn
                >
              </v-card-actions>
            </v-card>

            <v-divider class="mt-3 mb-5"></v-divider>
          </v-container>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import ProfileContent from "../components/ProfileContent";
import Axios from "axios";
import { baseAuthURI, baseURI } from "../api/api";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      changepasswordform: true,
      updateaddressform: true,
      showoldPass: false,
      shownewPass: false,
      showconfirmPass: false,
      changepass: false,
      user: {
        country: "",
        city: "",
        postal_code: "",
        address: "",
        phone: "",
      },
      oldpassword: "",
      newpassword: "",
      confirmpassword: "",
      passRules: [
        (v) => !!v || "Password is required",
        (v) =>
          (v && v.length >= 8) || "Password must be more than 8 characters",
      ],

      postalRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) =>
          v.length <= 6 ||
          "Postal code should be less than or equal to 6 characters",
      ],
      phoneRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) => v.length == 10 || "Phone number should be of 10 characters",
      ],
      shippingaddress: "",
    };
  },
  components: {
    ProfileContent,
  },

  mounted() {
    this.fetchUserInfo();
  },
  methods: {
    fetchUserInfo() {
      Axios.get(baseURI + "/user/info/" + localStorage.getItem("user_id"), {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        if (res.data.data[0].country == null) {
          this.user.country = "";
          this.user.city = "";
          this.user.postal_code = "";
          this.user.address = "";
        } else {
          this.user = res.data.data[0];
        }
      });
    },
    changePassword() {
      if (this.$refs.cform.validate()) {
        Axios.post(
          baseAuthURI + "/change-password",
          {
            oldPassword: this.oldpassword,
            password: this.newpassword,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            },
          }
        )
          .then((res) => {
            if (res.data.success) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "success",
                title: res.data.message,
              });
              this.changepass = false;
            } else {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "error",
                title: res.data.message,
              });
            }
          })
          .catch((err) => {
            if (err.response.status == 401) {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "error",
                title: err.response.data.message,
              });
            } else {
              const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                  toast.addEventListener("mouseenter", Swal.stopTimer);
                  toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
              });

              Toast.fire({
                icon: "error",
                title: "Something went wrong",
              });
            }
          });
      }
    },
    updateUserAddress() {
      if (this.$refs.aform.validate()) {
        Axios.post(`${baseURI}/user/shipping/update`, this.user, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        })
          .then(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 1500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "User address updated successfully",
            });
          })
          .catch(() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 1500,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "error",
              title: "Something went wrong",
            });
          });
      }
    },
  },
};
</script>
