<template>
  <v-container>
    <v-row>
      <v-col>
        <v-menu bottom transition="slide-y-transition">
          <template v-slot:activator="{ on, attrs }">
            <v-btn small text v-bind="attrs" v-on="on" class="text-capitalize">
              <v-icon class="mr-2">menu</v-icon>
              All Categories
            </v-btn>
          </template>
          <v-list dense v-if="categories != ''">
            <v-menu offset-x v-for="(item, i) in categories" :key="i">
              <template v-slot:activator="{ on, attrs }">
                <v-list-item>
                  <v-list-item-title
                    ><router-link
                      class="text-decoration-none black--text"
                      :to="'/category/products/' + item.slug"
                      >{{ item.name }}</router-link
                    >
                  </v-list-item-title>
                  <span class="float-right ml-5" v-bind="attrs" v-on="on"
                    ><v-icon color="red">keyboard_arrow_right</v-icon></span
                  >
                </v-list-item>
              </template>
              <v-list dense>
                <v-menu
                  offset-x
                  v-for="(subitem, j) in categories[i].sub_categories"
                  :key="j"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-list-item>
                      <v-list-item-title
                        ><router-link
                          class="text-decoration-none black--text"
                          :to="
                            '/category/sub-category/products/' + subitem.slug
                          "
                          >{{ subitem.name }}</router-link
                        >
                      </v-list-item-title>
                      <span
                        v-bind="attrs"
                        v-on="on"
                        class="float-right ml-5"
                        @click="fetchSubSubCategories(subitem.slug)"
                        ><v-icon color="red">keyboard_arrow_right</v-icon></span
                      >
                    </v-list-item>
                  </template>
                  <v-list dense v-if="subsubcategories != ''">
                    <v-menu
                      offset-x
                      v-for="(subsub, k) in subsubcategories"
                      :key="k"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-list-item>
                          <v-list-item-title>
                            <v-list-item-title
                              ><router-link
                                v-bind="attrs"
                                v-on="on"
                                class="text-decoration-none black--text"
                                :to="
                                  '/category/sub-sub-category/products/' +
                                  subsub.slug
                                "
                                >{{ subsub.name }}</router-link
                              >
                            </v-list-item-title>
                          </v-list-item-title>
                        </v-list-item>
                      </template>
                    </v-menu>
                  </v-list>
                  <v-list v-else dense>
                    <v-list-item dense>No Data</v-list-item>
                  </v-list>
                </v-menu>
              </v-list>
            </v-menu>
          </v-list>
        </v-menu></v-col
      >
      <v-col class="d-flex justify-end">
        <v-btn
          color="buttonColor"
          small
          tile
          class="mr-3 text-capitalize"
          dark
          to="/vendor"
          >Shop</v-btn
        >
        <v-btn
          color="buttonColor"
          small
          tile
          dark
          to="/user/cart"
          class="text-capitalize"
          >Cart</v-btn
        >
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Axios from "axios";
import { baseURI } from ".././api/api";
export default {
  data() {
    return {
      categories: [],
      subcategories: [],
      subsubcategories: [],
    };
  },

  created() {
    Axios.get(baseURI + "/categories")
      .then((res) => {
        this.categories = res.data.data;
      })
      .catch(() => {});
  },
  methods: {
    async fetchSubSubCategories(slug) {
      await Axios.get(baseURI + "/sub-categories/" + slug).then((res) => {
        this.subsubcategories = res.data.data[0].subSubCategories.data;
      });
    },
  },
};
</script>
<style scoped>
.right-chevron:hover {
  color: red;
}
</style>
