<template>
  <v-container>
    <v-row>
      <v-col lg="2" md="2" sm="3" v-for="(brand, i) in brands" :key="i">
        <v-hover v-slot="{ hover }">
          <v-card
            :elevation="hover ? 5 : 2"
            :class="{ 'on-hover': hover }"
            outlined
            :to="'/brands/products/' + brand.slug"
            max-width="200"

          >
            <v-img height="100px" :src="imageURI + brand.logo"></v-img>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";

export default {
  data() {
    return {
      imageURI: "",
      brands: [],
      image:
        "https://kinumna.com/public/uploads/brands/3tXH1PR2HHzVYXisSnzpnsyf7dYZh2xrH1Fez5w1.png",
    };
  },
  methods: {},
  created() {
    this.imageURI = baseImgURI;
    Axios.get(baseURI + "/brands").then((res) => {
      this.brands = res.data.data;
    });
  },
};
</script>
<style scoped></style>
