import Swal from "sweetalert2"
export default {
    namespaced: true,
    state: {
        compareList: [],
        productIds: []
    },
    getters: {
        getCompareList(state) {
            return state.compareList
        },
        getProductIds(state) {
            return state.productIds
        }

    },
    mutations: {
        addToCompareList(state, payload) {

            let compareProduct = state.compareList.find(
                (prod) => prod.id === payload.id
            );
            let categoryProduct = state.compareList.find(prod => prod.category === payload.category)
            if (categoryProduct || state.compareList.length == 0) {
                if (!compareProduct) {
                    state.compareList.push(payload)
                    const Toast = Swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timer: 3000,
                        timerProgressBar: true,
                        didOpen: (toast) => {
                            toast.addEventListener("mouseenter", Swal.stopTimer);
                            toast.addEventListener("mouseleave", Swal.resumeTimer);
                        },
                    });

                    Toast.fire({
                        icon: "success",
                        title: "Product added to comparelist successfully",
                    });
                }
            } else {
                const Toast = Swal.mixin({
                    toast: true,
                    position: "top-end",
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                        toast.addEventListener("mouseenter", Swal.stopTimer);
                        toast.addEventListener("mouseleave", Swal.resumeTimer);
                    },
                });

                Toast.fire({
                    icon: "info",
                    title: "Products of same category only. Remove product from comparelist.",
                });
            }

        },
        removeFromComparelist(state, payload) {
            let indexOfproduct = state.compareList.indexOf(payload);
            let indexofId = state.productIds.indexOf(payload.id)
            state.productIds.splice(indexofId, 1)
            state.compareList.splice(indexOfproduct, 1);
            const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                    toast.addEventListener("mouseenter", Swal.stopTimer);
                    toast.addEventListener("mouseleave", Swal.resumeTimer);
                },
            });

            Toast.fire({
                icon: "success",
                title: "Item Successfully removed from compare list",
            });
        },
        clearCompareList(state) {
            Swal.fire({
                title: "Warning!! this will remove all items from Compare list.",
                showDenyButton: true,
                showCancelButton: false,
                confirmButtonText: `Ok`,
                denyButtonText: `Cancel`,
            }).then((result) => {
                if (result.isConfirmed) {
                    state.compareList = []
                    state.productIds = []
                    const Toast = Swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timer: 2000,
                        timerProgressBar: true,
                        didOpen: (toast) => {
                            toast.addEventListener("mouseenter", Swal.stopTimer);
                            toast.addEventListener("mouseleave", Swal.resumeTimer);
                        },
                    });

                    Toast.fire({
                        icon: "success",
                        title: "Compare list successfully cleared",
                    });

                } else if (result.isDenied) {
                    Swal.fire("Deletion cancelled", "", "info");
                }
            });

        },
        storeProductIds(state, payload) {
            let prodId = state.productIds.find(e => e === payload.id)
            let categoryProduct = state.compareList.find(prod => prod.category === payload.category)
            if (categoryProduct || state.compareList.length == 0) {
                if (!prodId) {
                    state.productIds.push(payload.id)
                }
            }
        },
        clearCList(state){
            state.compareList = []
            state.productIds = []
        }

    },
    actions: {
        addToCompareList({ commit }, payload) {
            commit('addToCompareList', payload)

            commit('storeProductIds', payload)
        },


    }
}