<template>
  <div v-if="dealproducts != ''">
    <title-row :name="'/today/'" slug="todays-deal">Deal Of The Day</title-row>
    <product-sheet :products="dealproducts"></product-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
export default {
  data: () => ({
    dealproducts: [],
    imageURI: "",
  }),
  methods: {},
  created() {
    this.imageURI = baseImgURI;
    axios.get(baseURI + "/products/todays-deal").then((res) => {
      this.dealproducts = res.data.data;
    });
  },
};
</script>
