<template>
  <div v-if="featuredproducts != ''">
    <title-row :name="'/today/'" slug="featured">Featured Products</title-row>
    <product-sheet :products="featuredproducts"></product-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
export default {
  data: () => ({
    featuredproducts: [],
    imageURI: "",
  }),
  methods: {},
  created() {
    this.imageURI = baseImgURI;
    axios.get(baseURI + "/products/featured").then((res) => {
      this.featuredproducts = res.data.data;
    });
  },
};
</script>
