<template>
  <v-container>
    <v-row>
      <ProfileContent />

      <!-- if activeTab is order -->
      <v-col lg="8" md="8" sm="6">
        <purchase-history
          :orders="orders"
          :loading="loading"
          :loading_text="loading_text"
        ></purchase-history>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Axios from "axios";
import ProfileContent from "../components/ProfileContent";
import { baseURI } from "../api/api";
import PurchaseHistory from "../components/PurchaseHistory";
export default {
  data() {
    return {
      loading: true,
      loading_text: "Loading... Please wait",
      orders: [],
    };
  },
  mounted() {
    setTimeout(() => {
      Axios.get(
        baseURI + `/purchase-history/${localStorage.getItem("user_id")}`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        }
      ).then((res) => {
        if (res.status == 200) {
          this.loading = false;
          this.orders = res.data.data;
        } else {
          this.loading = false;
          this.loading_text = "No data available";
        }
      });
    }, 3000);
  },
  methods: {
    getColor(status) {
      if (status == "unpaid" || status == "pending") return "red";
      else return "green";
    },
    previewOrder(link) {
      this.preview = true;
      this.link = link;
      Axios.get(link, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        this.items = res.data.data;
      });
    },
  },
  components: {
    ProfileContent,
    PurchaseHistory,
  },
};
</script>
