<script>
// @ is an alias to /src
import FlashSales from "../components/FlashSale";
import DealsOfTheDay from "../components/DealsOfTheDay";
import FeaturedProduct from "../components/FeaturedProduct";
import ProductBanner from "../components/ProductBanner";
import ClassifiedAds from "../components/ClassifiedAds";
import BestSellers from "../components/BestSellers";
import TopBrandsAndCategories from "../components/TopBrandsAndCategories";
import CategoriesBannerBottom from "../components/CategoriesBannerBottom";
import CategoriesBannerTop from "../components/CategoriesBannerTop";
import JustForYou from "../components/JustForYou";
import { mapGetters } from "vuex";
import { baseURI, baseImgURI } from "../api/api";
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      imageURI: "",
      categoriesProducts: [],
      categories: [],
      topBanner: "",
      footerBanner: "",
      gridImages: [
        {
          photo:
            "https://miro.medium.com/max/2560/1*IPTRjl2GyBeNrBnbvB714A.jpeg",
        },
        {
          photo:
            "https://ktechgmbh.com/wp-content/uploads/2019/05/vuejs-plugins-blog-banner-1024x351.jpg",
        },
        {
          photo:
            "https://cdn-media-1.freecodecamp.org/images/Orco-Zk-h6lycc9sN1pwsyVdJTbaZNOpFqdh",
        },
      ],
    };
  },
  metaInfo: {
    title: "ZHOLAA | Ecommerce",
    titleTemplate: "%s - zholaa ecommerce site!",
    htmlAttrs: {
      lang: "en",
      amp: true,
    },
    meta: [
      { "http-equiv": "Content-Type", content: "text/html; charset=utf-8" },
      { name: "viewport", content: "width=device-width, initial-scale=1" },
      { name: "description", content: "Zholaa an ecommerce site Nepal" },
      {
        name: "keywords",
        content:
          "zholaa, zhola, ecommerce, nepal ecommerce, zhola shopping, zholaa, zhola Nepal, zholaa nepal ecommerce",
      },
    ],
  },

  computed: {
    ...mapGetters("CurrentUser", ["isauthenticated"]),
    auth() {
      return this.isauthenticated;
    },
  },
  components: {
    FlashSales,
    DealsOfTheDay,
    FeaturedProduct,
    ProductBanner,
    JustForYou,
    ClassifiedAds,
    BestSellers,
    TopBrandsAndCategories,
    CategoriesBannerBottom,
    CategoriesBannerTop,
  },
  mounted() {
    this.imageURI = baseImgURI;
    axios
