export default {
  namespaced: true,
  state: {
    wishlist_product_id: []
  },
  getters: {
    getWishlistProductId(state) {
      return state.wishlist_product_id
    }

  },
  mutations: {
    
    storeIdToWishlist(state,Id){
      let prodId = state.wishlist_product_id.find(e => e === Id)
        if (!prodId) {
          state.wishlist_product_id.push(Id)
        }
    },
    removeIdFromWishlist(state,Id){
      let indexOfproduct = state.wishlist_product_id.indexOf(Id);
      state.wishlist_product_id.splice(indexOfproduct, 1);
    },

    clearWishlist(state){
      state.wishlist_product_id = []
    }

  },
  actions: {
    storeWishlist({commit}, payload) {
      payload.forEach(element => {
        commit('storeIdToWishlist',element.product_id)
      });
    },
  },
  modules: {},
};
