import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import SingleProduct from "../views/SingleProduct.vue";
import CategoryWiseProducts from "../views/CategoryWiseProducts.vue";
import SubCategoryWiseProducts from "../views/SubCategoryWiseProducts.vue"
import SubSubCategoryWiseProducts from "../views/SubSubCategoryWiseProducts.vue"
import FeaturedWiseProducts from "../views/FeaturedWiseProducts.vue"
import LoginRegister from "../views/LoginRegister.vue";
import SendResetPasswordLink from "../views/SendResetPasswordLink"
import ResetPassword from "../views/ResetPassword"
import Affiliate from "../views/Affiliate.vue";
import Contact from "../views/Contact.vue";
import TrackOrder from "../views/TrackOrder.vue";
import SellOnZholaa from "../views/SellOnZholaa.vue"
import Checkout from "../views/Checkout.vue";
import Search from "../views/Search.vue";
import Cart from "../views/Cart.vue";
import Vendor from "../views/Vendor.vue";
import VendorVisit from "../views/VendorVisit";
import Account from "../views/Account.vue";
import Wishlist from "../views/Wishlist.vue";
import Address from "../views/Address"
import Order from "../views/Order.vue";
import Comparision from "../views/Comparision";
import Brands from "../views/Brands";
import Categories from "../views/Categories";
import BrandWiseProducts from "../views/BrandWiseProducts"
import CustomerPackage from "../views/CustomerPackage"
import OrderCompletion from "../views/OrderCompletion"

import Swal from "sweetalert2"


import store from "../store/index"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/order-completion",
    component: OrderCompletion
  },
  {
    path: "/single/product/:slug",
    name: "singleProduct",
    component: SingleProduct,
  },
  {
    path: "/brands/products/:slug",
    name: "brandsPage",
    component: BrandWiseProducts,
  },
  {
    path: "/category/products/:slug",
    name: "categoryProductPage",
    component: CategoryWiseProducts,
  },
  {
    path: "/category/sub-category/products/:slug",
    name: "SubcategoryProductPage",
    component: SubCategoryWiseProducts,
  },

  {
    path: "/category/sub-sub-category/products/:slug",
    name: "SubSubcategoryProductPage",
    component: SubSubCategoryWiseProducts,
  },
  {
    path: "/today/:slug",
    name: "FeaturedWiseProducts",
    component: FeaturedWiseProducts,
  },
  {
    path: "/user/login",
    name: "loginRegister",
    component: LoginRegister,
  },

  {
    path: '/user/resetpassword',
    name: 'SendResetPasswordLink',
    component: SendResetPasswordLink
  },
  {
    path: '/user/resetpassword/:token',
    name: 'resetPassword',
    component: ResetPassword
  },

  {
    path: "/affiliate",
    name: "affiliate",
    component: Affiliate,
  },
  {
    path: "/contact",
    name: "contact",
    component: Contact,
  },
  {
    path: '/customer-package',
    name: 'customerPackage',
    component: CustomerPackage
  },
  {
    path: "/user/account",
    name: "account",
    component: Account,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/user/wishlist",
    name: "wishlist",
    component: Wishlist,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/user/address",
    name: "address",
    component: Address,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/user/orders",
    name: "order",
    component: Order,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/track-order",
    name: "trackorder",
    component: TrackOrder,
  },
  {
    path: "/sell-on-zholaa",
    name: "sellonzholaa",
    component: SellOnZholaa,
  },
  {
    path: "/checkout/address",
    name: "checkout",
    component: Checkout,
  },
  {
    path: "/search/products",
    name: "search",
    component: Search,
  },
  {
    path: "/user/cart",
    name: "cart",
    component: Cart,
  },
  {
    path: "/vendor",
    name: "vendor",
    component: Vendor,
  },
  {
    path: "/vendor/visit/:slug",
    name: 'vendorVisit',
    component: VendorVisit
  },
  {
    path: "/brands",
    name: "brands",
    component: Brands,
  },
  {
    path: "/categories",
    name: "categories",
    component: Categories,
  },
  {
    path: "/compare",
    name: "comparision",
    component: Comparision,
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import("../views/About.vue"),
  },
  {
    path: "*",
    name: '404Error',
    component: () => import("../views/404Error.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
});




router.beforeEach((to, from, next) => {
  const tokenId = localStorage.getItem('token_id')
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  if (requiresAuth && (store.state.tokenId || tokenId == null)) {
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    Toast.fire({
      icon: "error",
      title: "Authorization Required",
    });
    const loginPath = window.location.pathname
    if (loginPath == '/user/login') {
      next({ name: 'loginRegister' })
    } else {
      next({ name: 'loginRegister', query: { redirect_url: loginPath } })
    }

  } else {
    next()
  }
})


export default router;
