<template>
  <div class="ml-5">
    <v-expansion-panels v-model="panel">
      <v-expansion-panel expand v-model="panel">
        <v-expansion-panel-header class="bg deep-purple lighten-5">
          Categories</v-expansion-panel-header
        >
        <v-expansion-panel-content>
          <v-list dense>
            <v-list-item
              dense
              v-for="(category, i) in categories"
              :key="i"
              class="black--text"
              :to="`/category/products/${category.slug}`"
            >
              <v-list-item-content v-if="category != category.name">
                <v-list-item-title>{{ category.name }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-expansion-panels>
      <v-expansion-panel
        v-for="(attribute, i) in attributes"
        :key="i"
        class="mt-2"
      >
        <v-expansion-panel-header class="bg deep-purple lighten-5"
          >Filter by {{ attribute.id }}</v-expansion-panel-header
        >
        <v-expansion-panel-content>
          <v-list dense>
            <v-list-item dense v-for="(item, i) in attribute.values" :key="i">
              <v-checkbox
                :label="item"
                dense
                hide-details
                @click="searchByAttribute(attribute.id, item)"
              ></v-checkbox>
            </v-list-item>
          </v-list>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-expansion-panels v-model="panel1" class="mt-2" v-if="colors!=''">
      <v-expansion-panel v-model="panel1">
        <v-expansion-panel-header class="bg deep-purple lighten-5"
          >Filter by Colors</v-expansion-panel-header
        >
        <v-expansion-panel-content>
          <v-row>
            <v-col v-for="(color, i) in colors" :key="i">
              <v-btn x-small :color="color" @click="searchByColor(color)">
                <v-icon v-if="$route.query.color == color" color="#6dd5ed">done</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-card elevation="0">
      <v-card-title>Price Range</v-card-title>

      <v-list-item>
        <v-list-item-content>
          <v-range-slider
            thumb-color="red"
            thumb-label="always"
            v-model="range"
            :max="max"
            :min="min"
            class="align-center my-7 mx-2"
            hide-details
            @change="search"
          >
          </v-range-slider>
        </v-list-item-content>
      </v-list-item>
    </v-card>
  </div>
</template>
<script>
export default {
  props: [
    "attributes",
    "colors",
    "categories",
    "searchProduct",
    "maxprice",
    "minprice",
  ],
  data() {
    return {
      min: 0,
      max: 100,
      range: [
        this.$route.query.min_price ? this.$route.query.min_price : 20,
        this.$route.query.max_price ? this.$route.query.max_price : 80,
      ],
      panel: 0,
      panel1: 0,
      selected: ["Small"],
      brands: [],
      sizes: [],
      availableColor: [],
      weightVariant: [],
      selectedBrand: "",
      selectedColor: "",
      done: [],
      selectedAttribute: {},
      picker: "",
      extraParam: {},
      testcolors: ["#E63E1A", "#1AE639", "#1A1DE6", "#09090A", "#FCFCFC"],
    };
  },
  methods: {
    searchByColor(data) {
      this.selectedColor = data;
      this.search();
    },

    search() {
      this.computeParam();
      this.searchProduct(
        Object.assign(this.extraParam, this.selectedAttribute, {
          sort_by: this.$route.query.sort_by,
          seller_id: this.$route.query.seller_id,
          brand: this.$route.query.brand,
        })
      );
    },

    searchByAttribute(id, value) {
      let obj = {};

      let key = `attribute_${id}`;
      obj[key] = value;
      this.selectedAttribute = Object.assign(this.selectedAttribute, obj);

      this.search();
    },

    computeParam() {
      this.extraParam["q"] = this.$route.query.q;
      this.extraParam["color"] = this.selectedColor;
      this.extraParam["min_price"] = this.range[0];
      this.extraParam["max_price"] = this.range[1];
    },
  },
};
</script>
