<template>
  <v-card elevation="0">
    <v-card-title>
      <v-list-item-title class="text-h4">Order Details</v-list-item-title>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
        color="red"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="orders"
      :items-per-page="10"
      :search="search"
      :loading="loading"
      :loading-text="loading_text"
    >
      <template v-slot:[`item.shipping_address`]="{}">
        <v-list dense>
          <v-list-item
            dense
            v-for="(address, i) in shippingAddress"
            :key="i"
            class="text-capitalize"
          >
            <v-list-item-icon style="width: 55px">{{ i }}:</v-list-item-icon>
            <v-list-item-content class="caption text-decoration-underline">
              {{ address }}
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </template>
      <template v-slot:[`item.payment_status`]="{ item }">
        <v-chip :color="getColor(item.payment_status)" dark>
          {{ item.payment_status }}
        </v-chip>
      </template>
      <template v-slot:[`item.order_details`]="{ item }">
        <v-icon color="red" @click="previewOrder(item.order_details)"
          >remove_red_eye</v-icon
        >
      </template>
    </v-data-table>
    <v-dialog v-model="preview">
      <v-card>
        <v-card-title
          >Purchased Items
          <v-spacer></v-spacer>
          <v-text-field
            v-model="itemsearch"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="itemheaders"
            :items="items"
            :items-per-page="10"
            class="elevation-1"
            :search="itemsearch"
          >
            <template v-slot:[`item.payment_status`]="{ item }">
              <v-chip :color="getColor(item.payment_status)" dark>
                {{ item.payment_status }}
              </v-chip>
            </template>
            <template v-slot:[`item.delivery_status`]="{ item }">
              <v-chip :color="getColor(item.delivery_status)" dark>
                {{ item.delivery_status }}
              </v-chip>
            </template>
            <template v-slot:[`item.shipping_cost`]="{item}">
              <v-chip v-if="item.shipping_cost == 0" color="green" dark>
                Free shipping
              </v-chip>
              <span v-else>{{ item.shipping_cost }}</span>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn dark color="red" @click="preview = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card-actions v-if="status == 'trackmodal'">
      <v-btn color="red" dark tile @click="closeModal" class="text-capitalize"
        >Close</v-btn
      >
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  props: ["orders", "shippingAddress", "status", "loading_text", "loading"],
  data() {
    return {
      search: "",
      itemsearch: "",
      preview: false,
      headers: [
        {
          text: "Order#",
          align: "start",
          sortable: false,
          value: "code",
        },
        {
          text: "Shipping Address",
          value: "shipping_address",
        },
        {
          text: "Payment Type",
          value: "payment_type",
        },
        {
          text: "Payment Status",
          value: "payment_status",
        },
        {
          text: "Payment Detail",
          value: "payment_details",
        },
        {
          text: "Coupon Discount",
          value: "coupon_discount",
        },
        {
          text: `Grand Total(${this.$currency})`,
          value: "grand_total",
        },
        {
          text: "Preview",
          value: "order_details",
          sortable: false,
        },
      ],
      itemheaders: [
        {
          text: "Item",
          value: "product",
          align: "start",
        },
        {
          text: "Variation",
          value: "variation",
        },
        {
          text: "Quantity",
          value: "quantity",
        },
        {
          text: `Price(${this.$currency})`,
          value: "price",
        },
        {
          text: "Tax",
          value: "tax",
        },
        {
          text: "Payment Status",
          value: "payment_status",
        },
        {
          text: "Delivery Status",
          value: "delivery_status",
        },
        {
          text: "Shipping Type",
          value: "shipping_type",
        },

        {
          text: "Shipping Cost",
          value: "shipping_cost",
        },
      ],
      link: "",
      items: [],
    };
  },

  methods: {
    closeModal() {
      this.$emit("close", false);
    },
    getColor(status) {
      if (status == "unpaid" || status == "pending") return "red";
      else return "green";
    },
    previewOrder(orderDetails) {
      this.preview = true;
      this.items = orderDetails;
    },
  },
};
</script>
