<template>
  <v-card>
    <v-card-title>
      Purchase History
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
        color="red"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="orders"
      :items-per-page="10"
      class="elevation-1"
      :search="search"
      :loading="loading"
      :loading-text="loading_text"
    >
      <template v-slot:[`item.code`]="{ item }">
        <v-list-item>
          <v-list-item-content>{{ item.code }}</v-list-item-content>
          <v-list-item-icon>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn text v-on="on" v-bind="attrs">
                  <v-dialog v-model="trackdialog">
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon
                        v-on="on"
                        v-bind="attrs"
                        @click="trackOrder(item.code)"
                        >track_changes</v-icon
                      >
                    </template>
                    <tracked-data
                      @close="trackdialog = $event"
                      :status="'trackmodal'"
                      :orders="myorders"
                      :shippingAddress="shippingAddress"
                      :loading_text="loading_text"
                      :loading="loading"
                    ></tracked-data>
                  </v-dialog>
                </v-btn>
              </template>
              <span>Track Order</span>
            </v-tooltip>
          </v-list-item-icon>
        </v-list-item>
      </template>
      <template v-slot:[`item.payment_status`]="{ item }">
        <v-chip :color="getColor(item.payment_status)" dark>
          {{ item.payment_status }}
        </v-chip>
      </template>
      <template v-slot:[`item.preview`]="{ item }">
        <v-icon color="red" @click="previewOrder(item.links.details)"
          >remove_red_eye</v-icon
        >
      </template>
    </v-data-table>
    <v-dialog v-model="preview">
      <v-card>
        <v-card-title
          >Purchased Items
          <v-spacer></v-spacer>
          <v-text-field
            v-model="itemsearch"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="itemheaders"
            :items="items"
            :items-per-page="10"
            class="elevation-1"
            :search="itemsearch"
            :loading="loading"
            :loading-text="loading_text"
          >
            <template v-slot:[`item.payment_status`]="{ item }">
              <v-chip :color="getColor(item.payment_status)" dark>
                {{ item.payment_status }}
              </v-chip>
            </template>
            <template v-slot:[`item.delivery_status`]="{ item }">
              <v-chip :color="getColor(item.delivery_status)" dark>
                {{ item.delivery_status }}
              </v-chip>
            </template>
            <template v-slot:[`item.shipping_cost`]="{ item }">
              <v-chip v-if="item.shipping_cost == 0" color="green" dark>
                Free shipping
              </v-chip>
              <span v-else>{{ item.shipping_cost }}</span>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn dark color="red" @click="preview = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>
<script>
import Axios from "axios";
import { baseURI } from "../api/api";
import TrackedData from "../components/TrackedData";
export default {
  props: ["orders", "loading", "loading_text"],
  data() {
    return {
      trackdialog: false,
      show: false,
      search: "",
      itemsearch: "",
      preview: false,
      headers: [
        {
          text: "Order#",
          align: "start",
          sortable: false,
          value: "code",
        },
        {
          text: "Date of Purchased",
          value: "date",
        },
        {
          text: "Payment Status",
          value: "payment_status",
        },
        {
          text: `Tax(${this.$currency})`,
          value: "tax",
        },
        {
          text: `Sub Total(${this.$currency})`,
          value: "subtotal",
        },
        {
          text: `Grand Total(${this.$currency})`,
          value: "grand_total",
        },
        {
          text: "Preview",
          value: "preview",
          sortable: false,
        },
      ],
      itemheaders: [
        {
          text: "Item",
          value: "product",
          align: "start",
        },
        {
          text: "Variation",
          value: "variation",
        },
        {
          text: "Quantity",
          value: "quantity",
        },
        {
          text: `Price(${this.$currency})`,
          value: "price",
        },
        {
          text: "Payment Status",
          value: "payment_status",
        },
        {
          text: "Payment Type",
          value: "payment_type",
        },
        {
          text: "Tax",
          value: "tax",
        },
        {
          text: "Shipping Cost",
          value: "shipping_cost",
        },
        {
          text: "Delivery Status",
          value: "delivery_status",
        },
      ],
      link: "",
      items: [],
      myorders: [],
      loader: false,
      shippingAddress: {},
    };
  },

  methods: {
    trackOrder(code) {
      this.myorders = [];
      this.loader = true;
      this.loading = true;
      this.loading_text = "Loading... Please wait";
      setTimeout(() => {
        Axios.get(`${baseURI}/track_your_order?order_code=${code}`).then(
          (res) => {
            if (res.status == 200) {
              this.loading = false;
              this.myorders.push(res.data.order);
              this.shippingAddress = JSON.parse(
                res.data.order.shipping_address
              );
            } else {
              this.loading = false;
              this.loading_text = "No data available";
            }
          }
        );
        this.loader = false;
      }, 1000);
    },
    getColor(status) {
      if (status == "unpaid" || status == "pending") return "red";
      else return "green";
    },
    previewOrder(link) {
      this.preview = true;
      this.link = link;
      this.loader = true;
      Axios.get(link, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      }).then((res) => {
        if (res.status == 200) {
          this.loading = false;
          this.loading_text = "Loading... Please wait";
          this.items = res.data.data;
        } else {
          this.loading = false;
          this.loading_text = "No data available";
        }
      });
    },
  },
  components: {
    TrackedData,
  },
};
</script>
