<template>
  <div>
    <v-container>
      <v-card elevation="5" v-if="tab == 'login'">
        <v-card-text>
          <v-row>
            <v-col lg="6" md="6" sm="12" class="hidden-sm-and-down">
              <v-img
                src="https://zholaa.nextnepal.dev/frontend/images/login.svg"
              ></v-img>
            </v-col>
            <v-col lg="6" md="6" sm="12">
              <v-card elevation="0">
                <v-card-title class="text-h5">Log In To Continue</v-card-title>
                <v-card-subtitle>
                  Welcome, please login to Your account</v-card-subtitle
                >
                <v-card-text>
                  <v-form
                    class="px-2"
                    ref="lform"
                    v-model="loginform"
                    @submit.prevent="onLogin"
                  >
                    Email
                    <v-text-field
                      v-model="email"
                      :rules="emailRules"
                      label="Your E-mail Address"
                      required
                      outlined
                      dense
                      single-line
                    ></v-text-field>
                    Password
                    <v-text-field
                      v-model="password"
                      :append-icon="showPass ? 'visibility' : 'visibility_off'"
                      :type="showPass ? 'text' : 'password'"
                      :rules="[(v) => !!v || 'Password is required']"
                      label="Your Password"
                      required
                      outlined
                      dense
                      single-line
                      @click:append="showPass = !showPass"
                    ></v-text-field>
                    <v-row class="mx-2">
                      <v-checkbox
                        v-model="remember_me"
                        label="Remember Me?"
                        color="red"
                      ></v-checkbox>
                      <v-spacer></v-spacer>
                      <router-link
                        class="text-decoration-none red--text d-flex align-center"
                        to="/user/resetpassword"
                        >Forgot password?</router-link
                      >
                    </v-row>
                    <v-row>
                      <v-col>
                        <div class="align-center">
                          Not a Member?
                          <span
                            ><v-btn
                              color="red"
                              text
                              @click="changeTab('register')"
                              >SignUp Now</v-btn
                            ></span
                          >
                        </div>
                      </v-col>
                      <v-col class="d-flex justify-end"
                        ><v-btn
                          tile
                          color="red"
                          :dark="loginform"
                          :disabled="!loginform"
                          type="submit"
                        >
                          SignIn
                        </v-btn></v-col
                      >
                    </v-row>
                    <!--  Please log into this webpage. -->
                  </v-form>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
          <v-row>
            <v-spacer class="hidden-xs-only"></v-spacer>
            <social-login></social-login>
          </v-row>
        </v-card-text>
      </v-card>
      <v-card elevation="5" v-if="tab == 'register'">
        <v-card-text>
          <v-row class="">
            <v-col lg="6" md="6" sm="12">
              <v-card-title class="text-h5">Register To Continue</v-card-title>
              <v-card-subtitle>
                Welcome, please Register to Your account</v-card-subtitle
              >
              <v-form
                class="px-2"
                ref="rform"
                @submit.prevent="onRegister"
                v-model="registerform"
              >
                <v-text-field
                  v-model="name"
                  label="Full Name"
                  :rules="nameRules"
                  required
                  outlined
                  dense
                ></v-text-field>

                <v-text-field
                  v-model="email"
                  :rules="emailRules"
                  label="Your E-mail Address"
                  required
                  outlined
                  dense
                ></v-text-field>

                <v-text-field
                  :append-icon="showPass1 ? 'visibility' : 'visibility_off'"
                  :type="showPass1 ? 'text' : 'password'"
                  v-model="password"
                  :rules="passRules"
                  label="Your Password"
                  required
                  outlined
                  dense
                  @click:append="showPass1 = !showPass1"
                ></v-text-field>

                <v-text-field
                  :append-icon="showPass2 ? 'visibility' : 'visibility_off'"
                  :type="showPass2 ? 'text' : 'password'"
                  v-model="password_confirmation"
                  :rules="[
                    this.password === this.password_confirmation ||
                      'Password must match',
                  ]"
                  label="Confirmation Password"
                  required
                  outlined
                  dense
                  @click:append="showPass2 = !showPass2"
                ></v-text-field>

                <v-text-field
                  type="number"
                  v-model="phonenumber"
                  label="Phone Number"
                  :rules="[(v) => !!v || 'Phone Number is required']"
                  required
                  outlined
                  dense
                ></v-text-field>
                <v-select
                  :items="items"
                  :rules="[(v) => !!v || 'Item is required']"
                  label="Are You?"
                  required
                  outlined
                  v-model="status"
                ></v-select>

                <v-btn
                  tile
                  color="red"
                  type="submit"
                  :dark="registerform"
                  :disabled="!registerform"
                >
                  SignUp
                </v-btn>
                <div>
                  Already a Member?
                  <span
                    ><v-btn color="red" text @click="changeTab('login')"
                      >SignIn Now</v-btn
                    ></span
                  >
                </div>
              </v-form>
            </v-col>
            <v-col lg="6" md="6" sm="12" class="image hidden-sm-and-down">
              <v-img
                src="https://zholaa.nextnepal.dev/frontend/images/login.svg"
              ></v-img>
            </v-col>
          </v-row>

          <v-row>
            <social-login></social-login>
          </v-row>
        </v-card-text>
      </v-card>
    </v-container>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import SocialLogin from "../components/SocialLogin";
export default {
  data() {
    return {
      loginform: true,
      registerform: true,
      showPass: false,
      showPass1: false,
      showPass2: false,
      tab: "login",
      remember_me: true,
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 30) || "Name must be less than 30 characters",
      ],
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      passRules: [
        (v) => !!v || "Password is required",
        (v) =>
          (v && v.length >= 8) || "Password must be more than 8 characters",
      ],

      name: "",
      email: "",
      password: "",
      password_confirmation: "",
      phonenumber: "",
      status: null,
      items: ["Buyer", "Seller"],
    };
  },
  methods: {
    ...mapActions("CurrentUser", ["onSignIn", "onSignUp"]),
    changeTab(data) {
      this.tab = data;
    },

    onLogin() {
      if (this.$refs.lform.validate()) {
        let authdata = {
          email: this.email,
          password: this.password,
          remember_me: this.remember_me,
        };

        this.onSignIn(authdata);
      }
    },

    onRegister() {
      let authdata = {
        name: this.name,
        email: this.email,
        password: this.password,
        password_confirmation: this.password_confirmation,
        phonenumber: this.phonenumber,
        status: this.status,
      };
      if (this.$refs.rform.validate()) {
        this.onSignUp(authdata);
      }
    },
  },
  components: {
    SocialLogin,
  },
};
</script>
<style scoped>
.image {
  height: 300px;
}
</style>
