<template>
  <v-container class="grid" v-if="topBrands != ''">
    <v-row>
      <div class="text-h5 title">Top 10 Brands</div>
      <v-spacer></v-spacer>
      <div>
        <v-btn small dark color="red" class="mr-3 text-capitalize" to="/brands">
          View All Brands
        </v-btn>
      </div>
    </v-row>
    <v-divider class="mt-3"></v-divider>
    <v-row class="items">
      <v-col cols="6" v-for="(item, i) in topBrands" :key="i">
        <v-hover v-slot="{ hover }">
          <v-card
            :elevation="hover ? 8 : 2"
            :class="{ 'on-hover': hover }"
            height="80px"
            :to="'/brands/products/' + item.slug"
          >
            <v-list-item three-line class="px-3">
              <v-list-item-avatar tile size="50" color="grey">
                <v-img :src="imageURI + item.logo"></v-img>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="text-subtitle-1 mb-1">{{
                  item.name
                }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";

export default {
  data() {
    return {
      topBrands: [],
      imageURI: "",
    };
  },
  methods: {},
  created() {
    Axios.get(baseURI + "/brands/top").then((res) => {
      this.topBrands = res.data.data;
    });
    this.imageURI = baseImgURI;
  },
};
</script>
