<script>
import Axios from "axios";
import { baseImgURI, baseURI } from "../api/api";

export default {
  data() {
    return {
      categories: [],
      imageURI: "",
      subCategoriesData: [],
      categorySubcategoryLink: [],
    };
  },
  methods: {
    fetchSubSubCategories(slug) {
      Axios.get(baseURI + "/sub-categories/" + slug).then((res) => {
        this.subsubcategories = res.data.data[0].subSubCategories.data;
      });
    },
  },
  created() {
    Axios.get(baseURI + "/categories")
      .then((res) => {
        this.categories = res.data.data;
        this.categorySubcategoryLink = this.categories.map(function (category) {
          return category.links.sub_categories;
        });

        Axios.all(this.categorySubcategoryLink.map((l) => Axios.get(l))).then(
          Axios.spread((...res) => {
            this.subCategoriesData = res;
          })
        );
      })
      .catch(() => {});

    this.imageURI = baseImgURI;
  },
};
</script>
