<template>
  <div>
    <v-card>
      <v-card-title class="bg grey lighten-3"> Total Amount </v-card-title>
      <v-card-text>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Item Subtotal</v-list-item-title>
          </v-list-item-content>
          <v-list-item-icon>
            {{ $currency }}.{{ getTotalPrice | twoDecimal }}
          </v-list-item-icon>
        </v-list-item>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Shipping Charge</v-list-item-title>
          </v-list-item-content>
          <v-list-item-icon v-if="getShippingCost != 0">
            {{ $currency }}.{{ getShippingCost | twoDecimal }}
          </v-list-item-icon>
          <v-list-item-icon v-else class="green px-1">
            Free Shipping
          </v-list-item-icon>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Estimate Total</v-list-item-title>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-list-item-subtitle v-on="on" v-bind="attrs"
                  >(inclusive with tax and discount)</v-list-item-subtitle
                >
              </template>
              <span>inclusive with tax and discount</span>
            </v-tooltip>
          </v-list-item-content>
          <v-list-item-icon
            >{{ $currency }}.{{
              getEstimatedTotal | twoDecimal
            }}</v-list-item-icon
          >
        </v-list-item>
      </v-card-text>
    </v-card>
