  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
import Axios from "axios";
import { baseURI } from "../api/api";
import { EventBus } from "../eventbus/event-bus";
export default {
  props: ["handlingFee"],
  data() {
    return {
      hint: "",
      loader: false,
      coupon: "coupon",
      cart: [],
    };
  },
  mounted() {
    this.fetchCartItems();
  },
  methods: {
    ...mapMutations("Checkout", ["storeGrandTotal"]),
    fetchCartItems() {
      if (localStorage.getItem("user_id")) {
        Axios.get(baseURI + "/carts", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((res) => {
            this.cartItems = res.data.data;
          })
          .catch(() => {});
      } else {
        this.fetchGuestCart();
      }
    },
    fetchGuestCart() {
      this.cartItems = this.getCartItems;
    },
    applyCoupon(coupon) {
      this.loader = true;
      this.hint = "";
      setTimeout(() => {
        if (coupon == "NextNepal") {
          this.hint = "Successfully Applied";
          this.color = "green";
        } else {
          this.hint = "Invalid coupon";
          this.color = "red";
        }
        this.loader = false;
      }, 3000);
    },
  },
  computed: {
    ...mapGetters("Cart", ["getCartItems"]),
    ...mapGetters("Checkout", ["getPaymentAmount"]),
    getTotalPrice() {
      let total = 0;
      if (this.cartItems) {
        this.cartItems.map((product) => {
          total += product.price * product.quantity;
        });
      }
      return total;
    },
    getShippingCost() {
      let totalShipping = 0;
      if (this.cartItems) {
        this.cartItems.map((product) => {
          totalShipping += product.shipping_cost;
        });
        if (this.handlingFee > 0) {
          totalShipping += this.handlingFee;
        }
      }
      return totalShipping;
    },
    getEstimatedTotal() {
      /* subtotal + shipping cost + tax */
      let estimatedtotal = 0;
      if (this.cartItems) {
        this.cartItems.map((product) => {
          estimatedtotal +=
            product.price * product.quantity +
            product.shipping_cost +
            product.tax * product.quantity;

          if (product.discount != null) {
            estimatedtotal -= product.discount;
          }
        });
      }
      if (this.handlingFee > 0) {
        estimatedtotal += this.handlingFee;
      }
      this.storeGrandTotal(estimatedtotal);
      return estimatedtotal;
    },
    cartItems: {
      get() {
        EventBus.$on("cart", (cart) => {
          this.cart = cart;
        });
        return this.cart;
      },
      set() {},
    },
  },
};
</script>

