<template>
  <div>
    <span
      v-for="(category, i) in categories"
      :key="i"
      v-show="i < categories.length / 2"
    >
      <span v-if="categoriesProducts[i] != null">
        <title-row :name="'/category/products/'" :slug="category.slug">{{
          category.name
        }}</title-row>
        <product-sheet
          :products="categoriesProducts[i].data.data"
        ></product-sheet>
      </span>
    </span>
  </div>
</template>
<script>
import Axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
export default {
  data() {
    return {
      imageURI: "",
      categories: [],
      categoriesProducts: [],
      categoryProductsLink: [],
    };
  },
  methods: {},
  async created() {
    this.imageURI = baseImgURI;
    await Axios.get(baseURI + "/categories")
      .then((res) => {
        this.categories = res.data.data;
        this.categoryProductsLink = this.categories.map(function (category) {
          return category.links.products;
        });

        Axios.all(this.categoryProductsLink.map((l) => Axios.get(l))).then(
          Axios.spread((...res) => {
            this.categoriesProducts = res;
          })
        );

        setTimeout(() => {
         this.$emit('categories', this.categories)
         this.$emit('categoriesProducts', this.categoriesProducts)
        }, 2000);
      })
      .catch(() => {});
  },
};
</script>
