<template>
  <v-form class="mt-5" ref="form" v-model="valid">
    <div class="font-weight-bold">Shipping Address</div>
    <v-divider class="mt-3"></v-divider>
    <v-container>
      <v-row>
        <v-col cols="12">
          <div class="text-subtitle-1 font-weight-bold">User Info</div>
          <v-divider></v-divider
        ></v-col>
        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          Full Name
          <v-text-field
            v-model="user.name"
            required
            outlined
            dense
            :readonly="auth ? true : false"
            :hint="auth ? 'Only guest user can modify name' : null"
            persistent-hint
          ></v-text-field>
        </v-col>
        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          Email
          <v-text-field
            v-model="user.email"
            required
            outlined
            dense
            :rules="emailRules"
            :readonly="auth ? true : false"
            :hint="auth ? 'Only guest user can modify email' : null"
            persistent-hint
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <div class="text-subtitle-1 font-weight-bold">User Address</div>
          <v-divider></v-divider
        ></v-col>
        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          Country
          <v-text-field
            v-model="addressInfo.country"
            required
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          Phone Number
          <v-text-field
            v-model="addressInfo.phone"
            type="number"
            required
            outlined
            :rules="phoneRule"
            dense
          ></v-text-field>
        </v-col>

        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          City
          <v-text-field
            v-model="addressInfo.city"
            required
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col lg="6" md="6" sm="12" style="min-width: 200px">
          Postal Code
          <v-text-field
            v-model="addressInfo.postal_code"
            type="number"
            required
            outlined
            dense
            :rules="postalRule"
          ></v-text-field>
        </v-col>
        <v-col lg="12" md="12" sm="12" style="min-width: 200px">
          Address
          <v-select
            v-model="addressInfo.address"
            :items="addressType"
            item-text="place"
            item-value="price"
            label="Select an Address"
            single-line
            outlined
            :hint="`${addressInfo.address.place} , ${$currency} ${addressInfo.address.price}`"
            return-object
          >
          </v-select>
        </v-col>
      </v-row>
      <v-divider></v-divider>
      <v-row>
        <v-col class="d-flex justify-start"
          ><v-btn
            outlined
            color="grey"
            tile
            to="/user/cart"
            class="text-capitalize"
            ><v-icon>keyboard_arrow_left</v-icon> Back to Cart</v-btn
          ></v-col
        ><v-col class="d-flex justify-end"
          ><v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-on="on"
                v-bind="attrs"
                :dark="valid"
                :disabled="!valid"
                color="red"
                tile
                @click="changeTab('Payment')"
                class="text-capitalize"
                >Continue <v-icon>keyboard_arrow_right</v-icon></v-btn
              >
            </template>
            <span>This will be your shipping address</span>
          </v-tooltip></v-col
        >
      </v-row>
    </v-container>
  </v-form>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import Axios from "axios";
import { baseURI } from "../api/api";

export default {
  data() {
    return {
      valid: true,
      user: {
        name: "",
        email: "",
      },
      addressInfo: {
        country: "",
        city: "",
        address: {
          place: "Inside Valley",
          price: 100,
        },

        phone: "",
        postal_code: "",
      },
      addressType: [
        {
          place: "Inside Valley",
          price: 100,
        },
        {
          place: "Outside Valley",
          price: 200,
        },
      ],
      emailRules: [(v) => /.+@.+\..+/.test(v) || "E-mail must be valid"],
      postalRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) =>
          v.length <= 6 ||
          "Postal code should be less than or equal to 6 characters",
      ],
      phoneRule: [
        (v) => /^[0-9]+$/.test(v) || "Only number allowed",
        (v) => v.length == 10 || "Phone number should be of 10 characters",
      ],
      address: {
        place: "Inside Valley",
        price: 100,
      },
    };
  },
  methods: {
    ...mapActions("Checkout", ["setAddress"]),

    changeTab(data) {
      if (this.$refs.form.validate()) {
        if (this.auth) {
          this.setAddress({
            country: this.addressInfo.country,
            city: this.addressInfo.city,
            postal_code: this.addressInfo.postal_code,
            phone: this.addressInfo.phone,
            address: this.addressInfo.address.place,
          });
        } else {
          this.setAddress({
            name: this.user.name,
            email: this.user.email,
            country: this.addressInfo.country,
            city: this.addressInfo.city,
            postal_code: this.addressInfo.postal_code,
            phone: this.addressInfo.phone,
            address: this.addressInfo.address.place,
          });
        }
        this.$emit("price", this.addressInfo.address.price);
        this.$emit("tab", data);
        this.$emit("e6", 1);
      }
    },
  },
  mounted() {
    if (this.auth) {
      let userApi = Axios.get(
        baseURI + "/user/info/" + localStorage.getItem("user_id"),
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token_id")}`,
          },
        }
      );
      let addressApi = Axios.get(baseURI + "/addresses", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token_id")}`,
        },
      });
      Axios.all([userApi, addressApi])
        .then(
          Axios.spread((...res) => {
            this.user = res[0].data.data[0];
            let temp = [];
            temp = res[1].data.data;

            if (temp.length == 0) {
              if (this.user.country == null) {
                this.addressInfo.country = "";
                this.addressInfo.phone = "";
                this.addressInfo.city = "";
                this.addressInfo.postal_code = "";
              } else {
                this.addressInfo = this.user;
                this.addressInfo.address = this.address;
              }
            } else {
              let foundaddress = temp.find((a) => a.set_default == 1);
              if (foundaddress != "") {
                this.addressInfo = foundaddress;
                this.addressInfo.address = this.address;
              } else {
                this.addressInfo.country = "";
                this.addressInfo.phone = "";
                this.addressInfo.city = "";
                this.addressInfo.postal_code = "";
              }
            }
          })
        )
        .catch(() => {});
    } else {
      this.user.name = "";
      this.user.email = "";
      this.addressInfo.country = "";
      this.addressInfo.phone = "";
      this.addressInfo.city = "";
      this.addressInfo.postal_code = "";
      this.addressInfo.address = this.address;
    }
  },
  computed: {
    ...mapGetters("CurrentUser", ["isauthenticated"]),
    auth() {
      return this.isauthenticated;
    },
  },
};
</script>
