<template>
  <div v-if="flashsales!=''">
    <v-card-title class="sheet text-h6 font-weight-bold">
      Flash Sales
      <!-- countdown timer -->
      <vue-countdown-timer
        class="ml-5 text-subtitle-2"
        :start-time="'2021-01-01 00:00:00'"
        :end-time="'2021-02-25 00:00:00'"
        :interval="1000"
        :start-label="'Until start:'"
        :end-label="'Until end:'"
        label-position="begin"
        :end-text="'Sales ended!'"
      >
        <template slot="start-label" slot-scope="scope">
          <span
            class="red--text"
            v-if="
              scope.props.startLabel !== '' &&
              scope.props.tips &&
              scope.props.labelPosition === 'begin'
            "
            >{{ scope.props.startLabel }}:</span
          >
          <span
            style="color: green"
            v-if="
              scope.props.endLabel !== '' &&
              !scope.props.tips &&
              scope.props.labelPosition === 'begin'
            "
            >{{ scope.props.endLabel }}:</span
          >
        </template>

        <template slot="countdown" slot-scope="scope">
          <span>{{ scope.props.days }} days {{ scope.props.dayTxt }}</span>
          <span>{{ scope.props.hours }} hours {{ scope.props.hourTxt }} </span>
          <span
            >{{ scope.props.minutes }} minutes{{ scope.props.minutesTxt }}
          </span>
          <span>{{ scope.props.seconds }} seconds</span>
        </template>

        <template slot="end-label" slot-scope="scope">
          <span
            style="color: red"
            v-if="
              scope.props.startLabel !== '' &&
              scope.props.tips &&
              scope.props.labelPosition === 'end'
            "
            >{{ scope.props.startLabel }}:</span
          >
          <span
            style="color: blue"
            v-if="
              scope.props.endLabel !== '' &&
              !scope.props.tips &&
              scope.props.labelPosition === 'end'
            "
            >{{ scope.props.endLabel }}:</span
          >
        </template>

        <template slot="end-text" slot-scope="scope">
          <span style="color: red">{{ scope.props.endText }}</span>
        </template>
      </vue-countdown-timer>
      <v-spacer></v-spacer>
      <v-btn
        small
        dark
        color="red"
        class="today_link text-capitalize"
        :to="{
          name: 'FeaturedWiseProducts',
          params: {
            slug: 'flash-sale',
          },
        }"
      >
        View More
      </v-btn>
    </v-card-title>
    <v-sheet class="mt-5 sheet" elevation="8">
      <v-slide-group v-model="model" active-class="success" show-arrows>
        <v-slide-item v-for="(item, n) in flashsales" :key="n" v-show="n < 15">
          <v-hover v-slot="{ hover }">
            <v-card
              :elevation="hover ? 16 : 5"
              :class="{ 'on-hover': hover }"
              class="ma-3"
              height="220"
              width="205"
              :to="'/single/product/' + item.slug"
            >
              <v-img
                v-if="item.featured_image"
                :src="imageURI + item.featured_image"
                height="100px"
              ></v-img>
              <v-img v-else class="bg grey" height="100px"></v-img>
              <v-card-title
                class="text-subtitle-2 d-inline-block text-truncate"
                style="max-width: 200px"
                >{{ item.name }}
              </v-card-title>

              <v-row class="px-2">
                <v-col cols="6" class="d-flex justify-center"
                  ><v-card-subtitle
                    >{{ $currency }}.{{
                      item.base_discounted_price | twoDecimal
                    }}
                  </v-card-subtitle></v-col
                >

                <v-col cols="6" class="d-flex justify-center"
                  ><v-card-subtitle class="text-decoration-line-through"
                    >{{ $currency }}.{{ item.base_price | twoDecimal }}
                  </v-card-subtitle></v-col
                >
              </v-row>
            </v-card>
          </v-hover>
        </v-slide-item>
      </v-slide-group>
    </v-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { baseURI, baseImgURI } from "../api/api";
export default {
  data: () => ({
    model: null,
    flashsales: [],
    imageURI: "",
  }),
  methods: {},
  created() {
    this.imageURI = baseImgURI;
    axios.get(baseURI + "/products/flash-sale").then((res) => {
      this.flashsales = res.data.data;
    });
  },
};
</script>

